FindlySDK.prototype.enableAutoSuggest = function () {
  var _self = this;
  _self.pubSub.subscribe("sa-auto-suggest", (msg, data) => {
    _self.getSuggestion(data);
  });
};
FindlySDK.prototype.getSuggestion = function (suggestions) {
  var _self = this;
  var $suggest;
  var needle;
  if (!$("body").hasClass("top-down")) {
    $suggest = $(".bottom-up-suggestion");
    // some other key was pressed
    needle = $(".bottom-up-search").val();
  } else {
    if (_self.vars.enterIsClicked) {
      return;
    }
    $suggest = $(".top-down-suggestion");
    needle = $(".search-top-down").val();
  }

  // is the field empty?
  if (!$.trim(needle).length) {
    $suggest.val("");
    if ($("#live-search-result-box").find(".suggestion-box").length) {
      $(".suggestion-box").remove();
    }
    $("#live-search-result-box").hide();
    return false;
  }

  _self.vars.showingMatchedResults = false;

  if (suggestions.length) {
    var searchQuery = $.trim(needle);
    var searchQueryArr = searchQuery.split(" ");
    if (searchQueryArr.length) {
      if (searchQueryArr[searchQueryArr.length - 1] && suggestions[0]) {
        suggestions[0] =
          searchQueryArr[searchQueryArr.length - 1] +
          suggestions[0].slice(
            searchQueryArr[searchQueryArr.length - 1].length,
            suggestions[0].length
          );
      }
      searchQueryArr[searchQueryArr.length - 1] = suggestions[0];
    }
    searchQuery = searchQueryArr.join(" ");
    $suggest.val(searchQuery);
  }
  if (!suggestions.length) {
    $suggest.val("");
  }
  // //top-down-search//
  // _self.showSuggestionbox(suggestions);
};
$(document).mouseup(function (e) {
  var contextVarAreaContainer = $(".context_variables_textarea"); // Text area dialog
  var contestVarContainer = $(".contest_variables_dropdown"); // Context menu
  if (
    !contextVarAreaContainer.is(e.target) &&
    contextVarAreaContainer.has(e.target).length === 0 &&
    !contestVarContainer.is(e.target) &&
    contestVarContainer.has(e.target).length === 0
  ) {
    contextVarAreaContainer.hide();
    contestVarContainer.hide();
  }
});

