FindlySDK.prototype.sendMessageToSearch = function (
  type,
  mesageData,
  data,
  isSearchResultsMessage
) {
  var _self = this;
  var messageData = {
    text: "",
    from: type,
  };
  var devMode = _self.isDev ? "true" : "false";
  var viewType = _self.vars.customizeView ? "Customize" : "Preview";
  $(".more-results").css("display", "none");
  $('.feedback-template-positions.if-live-search-top-down.bottom-up-show-all').css('display', 'flex');
  if (
    type === "user" &&
    ($("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val()) !== null &&
    ($("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val()) !== undefined
  ) {
    messageData.text = $("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val();
    if (
      messageData.text &&
      messageData.text.trim() &&
      !_self.customSearchResult
    ) {
      if (isSearchResultsMessage) {
        messageData.isSearchResultsMessage = true;
      }
      messageData.timeStamp = moment().format('LT');
      var template = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
        msgData: messageData,
        devMode: devMode,
        viewType: viewType,
        helpers: helpers,
      });
      $("#searchChatContainer").append(template);
      var scrollBottom =
        $("#searchChatContainer").scrollTop() +
        $("#searchChatContainer").height();
      $("#searchChatContainer").animate({ scrollTop: scrollBottom });
      if (!$("body").hasClass("top-down")) {
        setTimeout(function () {
          if (
            $(".searchAssist-kore-chat-window.search-container").hasClass(
              "active"
            )
          ) {
            $(".typingIndicatorContent").css("display", "block");
            $("#searchChatContainer")
              .off("scroll")
              .on("scroll", function (event) {
                if (
                  $(".userMessage").last().parent() &&
                  $(".userMessage")
                    .last()
                    .parent()
                    .next()
                    .find(".messageBubble-content").length
                ) {
                  $(".typingIndicatorContent").css("display", "none");
                }
              });
          } else {
            _self.showTypingIndicator();
          }
        }, 200);
      }
    }
  }
  if (
    type === "user-conversation" &&
    $("#sa-conversation-box").val() !== null &&
    $("#sa-conversation-box").val() !== undefined
  ) {
    messageData.text = $("#sa-conversation-box").val();
    messageData.from = "user";
    messageData.timeStamp = moment().format('LT');
    if (
      (messageData.text &&
        messageData.text.trim() &&
        _self.customSearchResult &&
        !$("body").hasClass("top-down"))
      // ($("body").hasClass("top-down") &&
      //   messageData.text &&
      //   messageData.text.trim() &&
      //   !_self.customSearchResult)
    ) {
      var template = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
        msgData: messageData,
        devMode: devMode,
        viewType: viewType,
        helpers: helpers,
      });
      $("#searchChatContainer").append(template);
    }
  }
  if (type === "bot") {
    clearTimeout(indicatorTimer);
    $(".typingIndicatorContent").css("display", "none");
    try {
      messageData = JSON.parse(mesageData);
    } catch (error) {
      messageData.text = mesageData;
    }
    var messagemessageHtmlHtml = "";
    var defaultBotMessage = {
      type: "bot_response",
      from: "bot",
      message: [{ type: "text", component: {} }],
    };
    if (
      messageData &&
      typeof messageData.text == "object" &&
      (messageData.text.type == "image" ||
        messageData.text.type == "audio" ||
        messageData.text.type == "video" ||
        messageData.text.type == "link" ||
        messageData.text.type == "message")
    ) {
      defaultBotMessage.message[0].component = messageData.text;
      if (messageData.text.cInfo) {
        defaultBotMessage.message[0].cInfo = messageData.text.cInfo;
      }
    } else {
      defaultBotMessage.message[0].component = messageData;
    }
    defaultBotMessage.messageId = koreGenerateUUID();
    var messageHtml = _self.customTemplateObj.renderMessage(defaultBotMessage);
    if (!messageHtml || !messageHtml.length) {
      if (messageData && messageData.type && messageData.type === "template") {
        //removed custom templates
      } else {
        //simple text message
        if (isSearchResultsMessage) {
          messageData.isSearchResultsMessage = true;
        }
        if (messageData.text && typeof messageData.text == "object") {
          messageData.text = messageData.text.text;
        }
        if (_typeof(messageData) == "number") {
          messageHtml = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
            msgData: {
              from: "bot",
              text: messageData,
            },
            helpers: helpers,
          });
          $("#searchChatContainer").append(messageHtml);
        } else {
          messageHtml = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
            msgData: messageData,
            helpers: helpers,
          });
          // $('#searchChatContainer').append(template);
        }
      }
    }
    if (messageData.payload && messageData.payload.text && !messageHtml) {
      var template = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
        msgData: {
          from: "bot",
          text: messageData.payload.text,
        },
        helpers: helpers,
      });
      $("#searchChatContainer").append(template);
    }
    if (
      $(messageHtml).find(".barchart").length ||
      $(messageHtml).find(".linechart").length ||
      $(messageHtml).find(".tableChart").length ||
      $(messageHtml).find(".pieChart").length
    ) {
      $(messageHtml).find(".messageBubble").addClass("hide");
    }
    $("#searchChatContainer").append(messageHtml);
    setTimeout(() => {
      if (
        $(".messageBubble").last().find(".messageBubble-content").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        !$(".messageBubble-content")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".messageBubble-content").last().parent().position().top -
              25,
          },
          500
        );
      } else if (
        $(".messageBubble").last().find(".messageBubble-content").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        $(".messageBubble-content")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".messageBubble-content").last().parent().position().top -
              25 -
              $(".messageBubble-content")
                .last()
                .parent()
                .prev()
                .find(".messageBubble-content")
                .height(),
          },
          500
        );
      } else if (
        $(".messageBubble").last().find(".fromOtherUsers").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        $(".fromOtherUsers")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".fromOtherUsers").last().parent().position().top -
              25 -
              $(".fromOtherUsers")
                .last()
                .parent()
                .prev()
                .find(".messageBubble-content")
                .height(),
          },
          500
        );
      } else {
        if (
          $(".userMessage").last().parent().position() &&
          ($(".userMessage").last().parent().position().top > 34 ||
            $(".userMessage").last().parent().position().top < 34)
        ) {
          $("#searchChatContainer").animate(
            {
              scrollTop:
                $("#searchChatContainer").scrollTop() +
                $(".userMessage").last().parent().position().top -
                25,
            },
            500
          );
        }
      }
    }, 200);
  }
  if (type === "botAction") {
    messageData.text = _self.vars.searchObject.searchText;
    messageData.from = "user";
    messageData.timeStamp = moment().format('LT');
    var template = $(_self.getSearchTemplate("messageBubbles")).tmplProxy({
      msgData: messageData,
      devMode: devMode,
      viewType: viewType,
      helpers: helpers,
    });
    //$('#searchChatContainer').append(template);
    $(".search-body").hide();
    $("#searchChatContainer").addClass("bgfocus-override");
    _self.bindLiveDataToChat(true);
  }
  setTimeout(function () {
    if (_self.isDev == false) {
      // var scrollBottom = $('#searchChatContainer').scrollTop() + $('#searchChatContainer').height();
      // $('#searchChatContainer').animate({ scrollTop: scrollBottom });
      if (
        $(".messageBubble").last().find(".messageBubble-content").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        !$(".messageBubble-content")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".messageBubble-content").last().parent().position().top -
              25,
          },
          500
        );
      } else if (
        $(".messageBubble").last().find(".messageBubble-content").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        $(".messageBubble-content")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".messageBubble-content").last().parent().position().top -
              25 -
              $(".messageBubble-content")
                .last()
                .parent()
                .prev()
                .find(".messageBubble-content")
                .height(),
          },
          500
        );
      } else if (
        $(".messageBubble").last().find(".fromOtherUsers").length &&
        !$(".messageBubble-content").last().parent().prev().find(".userMessage")
          .length &&
        $(".fromOtherUsers")
          .last()
          .parent()
          .prev()
          .find(".messageBubble-content").length
      ) {
        $("#searchChatContainer").animate(
          {
            scrollTop:
              $("#searchChatContainer").scrollTop() +
              $(".fromOtherUsers").last().parent().position().top -
              25 -
              $(".fromOtherUsers")
                .last()
                .parent()
                .prev()
                .find(".messageBubble-content")
                .height(),
          },
          500
        );
      } else {
        if (
          $(".userMessage").last().parent().position() &&
          ($(".userMessage").last().parent().position().top > 34 ||
            $(".userMessage").last().parent().position().top < 34)
        ) {
          $("#searchChatContainer").animate(
            {
              scrollTop:
                $("#searchChatContainer").scrollTop() +
                $(".userMessage").last().parent().position().top -
                25,
            },
            500
          );
        }
      }
    }
  }, 200);
};

FindlySDK.prototype.userLogin = function (clickedAction) {
  _self = this;
  var action = clickedAction;
  var userLogin = $(_self.getSearchTemplate("userLogin")).tmplProxy({});
  $(userLogin)
    .off("keyup", "#testFutureUserId")
    .on("keyup", "#testFutureUserId", function (event) {
      $(".errorMsg").addClass("hide");
      if ($("#testFuturePassword").val() && $("#testFutureUserId").val()) {
        $(".testFutureLoginBtn").removeAttr("disabled");
        $(".testFutureLoginBtn").css("cursor", "pointer");
        $(".testFutureLoginBtn").css("opacity", "1");
      } else {
        $(".testFutureLoginBtn").attr("disabled");
        $(".testFutureLoginBtn").css("cursor", "auto");
        $(".testFutureLoginBtn").css("opacity", "0.7");
      }
    });
  $(userLogin)
    .off("keyup", "#testFuturePassword")
    .on("keyup", "#testFuturePassword", function (event) {
      $(".errorMsg").addClass("hide");
      if ($("#testFuturePassword").val() && $("#testFutureUserId").val()) {
        $(".testFutureLoginBtn").removeAttr("disabled");
        $(".testFutureLoginBtn").css("cursor", "pointer");
        $(".testFutureLoginBtn").css("opacity", "1");
      } else {
        $(".testFutureLoginBtn").attr("disabled");
        $(".testFutureLoginBtn").css("cursor", "auto");
        $(".testFutureLoginBtn").css("opacity", "0.7");
      }
    });
  $(userLogin)
    .off("click", ".testFutureLoginBtn")
    .on("click", ".testFutureLoginBtn", function (event) {
      event.stopPropagation();
      if (
        $("#testFutureUserId").val() === "John" &&
        $("#testFuturePassword").val() === "1111"
      ) {
        _self.vars.loggedInUser = "John";
        $(".menu-link.login").addClass("hide");
        $(".menu-link.loggedIn").removeClass("hide");
        $(".errorMsg").addClass("hide");
        //static pay bill removed
        if (false && action === "pay bill") {
          _self.searchEventBinding("pay bill", action, event, {});
        } else {
          _self.vars.searchObject.searchText = action;
          _self.sendMessageToSearch("botAction");
        }
      } else {
        $(".errorMsg").removeClass("hide");
      }
    });
  $(".search-body .finalResults").addClass("hide");
  $(".searchBox").addClass("hide");
  $(".search-body").append(userLogin);
  $(".search-body").removeClass("hide");
  // $('#searchChatContainer').addClass('bgfocus');
};
FindlySDK.prototype.getFrequentlySearched = function (url, type, payload) {
  var bearer =
    "bearer " + this.bot.options.accessToken ||
    this.API.jstBarrer ||
    "bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiIxMjM0NTY3ODkwIiwibmFtZSI6IkpvaG4gRG9lIiwiaWF0IjoxNTE2MjM5MDIyfQ.wrUCyDpNEwAaf4aU5Jf2-0ajbiwmTU3Yf7ST8yFJdqM";
  var headers = {};

  payload = JSON.parse(payload);

  headers["Authorization"] = bearer;
  headers["Content-Type"] = "application/json";

  var currentDate = new Date();
  var dateTime =
    currentDate.getDate() +
    "/" +
    (currentDate.getMonth() + 1) +
    "/" +
    currentDate.getFullYear() +
    ", " +
    currentDate.getHours() +
    ":" +
    currentDate.getMinutes() +
    ":" +
    currentDate.getSeconds();

  payload.userId = this.bot.userInfo.userInfo.userId;

  payload.streamId = this.bot.options.botInfo.taskBotId;
  var _self = this;
  _self.vars.deselectedAutoFilters = [];
  if (!$("body").hasClass("demo")) {
    payload.indexPipelineId = _self.API.indexpipelineId;
    payload.queryPipelineId = _self.API.pipelineId;
  }

  payload["messagePayload"] = {
    clientMessageId: new Date().getTime(),
    message: {
      body: payload.query,
    },
    resourceId: "/bot.message",
    timeDateDay: dateTime,
    currentPage: window.location.href,
    meta: {
      timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      locale: window.navigator.userLanguage || window.navigator.language,
    },
    location: this.vars.locationObject.location,
    country: this.vars.locationObject.country,
  };

  if (url != this.API.livesearchUrl) {
    if (!payload.maxNumOfResults) {
      payload["maxNumOfResults"] = 5;
    }
    if (!payload.pageNumber) {
      payload["pageNumber"] = 0;
    }
  }
  if (this.bot.options) {
    payload.messagePayload["client"] = this.bot.options.client || "sdk";
    payload.messagePayload["botInfo"] = {};
    payload.messagePayload["botInfo"].chatBot =
      this.bot.options.botInfo.chatBot;
    payload.messagePayload["botInfo"].taskBotId =
      this.bot.options.botInfo.taskBotId;
    payload.messagePayload["botInfo"].customData = {};
    var contextObj = $("#contextjsonfield").val();
    if (contextObj) {
      contextObj.trim();
      if (contextObj) {
        try {
          contextObj = JSON.parse(contextObj);
        } catch (error) {
          contextObj = {};
        }
      }
      this.bot.options.botInfo.customData["userContext"] = contextObj;
      payload.messagePayload["botInfo"].customData =
        this.bot.options.botInfo.customData;
    } else {
      if (window.localStorage.getItem("userName")) {
        this.bot.options.botInfo.customData["userContext"] = {
          user_name: window.localStorage.getItem("userName"),
          location: window.localStorage.getItem("userLocation"),
          gender: window.localStorage.getItem("gender"),
          user_age: window.localStorage.getItem("userAge"),
        };
      }
      payload.messagePayload["botInfo"].customData =
        _self.bot.options.botInfo.customData || {};
    }
  }

  payload = JSON.stringify(payload);

  if (!_self.isDev) {
    if (_self.config.botOptions.assertion) {
      headers.auth = _self.config.botOptions.assertion;
    }
  }

  return $.ajax({
    url: url,
    type: type,
    dataType: "json",
    /*headers: {
          "Authorization": bearer,
          "Content-Type": "application/json"
        },*/
    headers: headers,
    data: payload,
    success: function (data) { },
    error: function (err) { },
  });
};

