FindlySDK.prototype.addSearchResult = function (config) {
  var _self = this;
  _self.customSearchResult = false;
  var structuredDataContainer = "";
  if ($("body").hasClass("top-down")) {
    var actionsPosition = "top";
    if (searchConfigurationCopy && searchConfigurationCopy.botConfig) {
      actionsPosition =
        searchConfigurationCopy.botConfig.botActionResultsExperience;
    }
    let actionParentContainer = `<div id="actions-container" class="quick-actions-container actions-full-search-container"></div>`;
    if (actionsPosition == "top") {
      $(".content-data-sec").prepend(actionParentContainer);
    } else {
      $(".full-search-data-container").after(actionParentContainer);
    }
  }
  _self.pubSub.unsubscribe("sa-search-result");
  _self.pubSub.subscribe("sa-search-result", (msg, data) => {
    if (!data.selectedFacet) {
      data.selectedFacet = "all results";
      if(data.isFullResults){
      _self.pubSub.publish("facet-selected", { selectedFacet: "all results" });
      }
    }
    if (data.selectedFacet && data.selectedFacet === "faq") {
      data.web = [];
      data.files = [];
      data.tasks = [];
    }
    if (data.selectedFacet && data.selectedFacet === "web") {
      data.faqs = [];
      data.files = [];
      data.tasks = [];
    }
    if (data.selectedFacet && data.selectedFacet === "file") {
      data.faqs = [];
      data.web = [];
      data.tasks = [];
    }
    if (data.selectedFacet && data.selectedFacet === "task") {
      data.faqs = [];
      data.web = [];
      data.files = [];
    }
    var pageContainer = "";
    var faqContainer = "";
    var actionContainer = "";
    if (config.container) {
      pageContainer = faqContainer = actionContainer = config.container;
    }
    if (config.pageContainer) {
      pageContainer = config.pageContainer;
    }
    if (config.faqContainer) {
      faqContainer = config.faqContainer;
    }
    if (config.actionContainer) {
      actionContainer = config.actionContainer;
    }
    if (pageContainer) {
      if (config.pageTemplateId) {
        var dataHTML = $("#" + config.pageTemplateId).tmplProxy(data);
        $("#" + pageContainer)
          .empty()
          .append(dataHTML);
      } else if (config.pageTemplate) {
        var dataHTML = $(config.pageTemplate).tmplProxy(data);
        $("#" + pageContainer)
          .empty()
          .append(dataHTML);
      }
    }
    if (faqContainer) {
      if (config.faqTemplateId) {
        var dataHTML = $("#" + config.faqTemplateId).tmplProxy(data);
        if (
          faqContainer !== pageContainer &&
          faqContainer !== actionContainer
        ) {
          $("#" + faqContainer).empty();
        }
        $("#" + faqContainer).append(dataHTML);
      } else if (config.faqTemplate) {
        var dataHTML = $(config.faqTemplate).tmplProxy(data);
        if (
          faqContainer !== pageContainer &&
          faqContainer !== actionContainer
        ) {
          $("#" + faqContainer).empty();
        }
        $("#" + faqContainer).append(dataHTML);
      }

      if (actionContainer) {
        if (!$("body").hasClass("top-down")) {
          if (data.tasks && data.tasks.length > 0) {
            if (config.actionTemplateId) {
              var dataHTML = $("#" + config.actionTemplateId).tmplProxy(data);
              if (
                actionContainer !== pageContainer &&
                actionContainer !== faqContainer
              ) {
                $("#" + actionContainer).empty();
              }
              $("#" + actionContainer).append(dataHTML);
            } else if (config.actionTemplate) {
              var dataHTML = $(config.actionTemplate).tmplProxy(data);
              if (
                actionContainer !== pageContainer &&
                actionContainer !== faqContainer
              ) {
                $("#" + actionContainer).empty();
              }
              $("#" + actionContainer).append(dataHTML);
            }
          } else if (config.actionTemplate) {
            var dataHTML = $(config.actionTemplate).tmplProxy(data);
            if (
              actionContainer !== pageContainer &&
              actionContainer !== faqContainer
            ) {
              $("#" + actionContainer).empty();
            }
            $("#" + actionContainer).append(dataHTML);
          }
        } else {
          if (!data.isLiveSearch && $("body").hasClass("top-down")) {
            if (config.actionTemplateId) {
              if (config.actionTemplateId === "actions-template") {
                var viewType = _self.vars.customizeView
                  ? "Customize"
                  : "Preview";
                var devMode = _self.isDev ? true : false;
                data = {
                  ...data,
                  ...{
                    appearanceType: "task",
                    devMode: devMode,
                    viewType: viewType,
                  },
                };
                var dataHTML = $(_self.getTopDownActionTemplate()).tmplProxy(
                  data
                );
                if (
                  actionContainer !== pageContainer &&
                  actionContainer !== faqContainer
                ) {
                  $("#" + actionContainer).empty();
                }
                $("#" + actionContainer).append(dataHTML);
                // _self.bindCarouselForActionsTemplate("#" + actionContainer);
              } else {
                var dataHTML = $("#" + config.actionTemplateId).tmplProxy(data);
                if (
                  actionContainer !== pageContainer &&
                  actionContainer !== faqContainer
                ) {
                  $("#" + actionContainer).empty();
                }
                $("#" + actionContainer).append(dataHTML);
              }
            } else if (config.actionTemplate) {
              var dataHTML = $(config.actionTemplate).tmplProxy(data);
              if (
                actionContainer !== pageContainer &&
                actionContainer !== faqContainer
              ) {
                $("#" + actionContainer).empty();
              }
              $("#" + actionContainer).append(dataHTML);
            }
          }
        }

        if (config.actionHandler) {
          _self.pubSub.subscribe("sa-action-clicked", (topic, data) => {
            config.actionHandler(data);
          });
        }
        _self.bindSearchActionEvents();
      } else {
        $("#" + actionContainer).empty();
      }
    }

    if (config.searchHandler) {
      config.searchHandler(data);
    }
  });
  if (config.structuredDataContainer) {
    structuredDataContainer = config.structuredDataContainer;
  }
  if (structuredDataContainer) {
    // _self.pubSub.subscribe("sa-register-all-template", (msg, data) => {
    //   _self.registerAllTemplateConfig(data, _self.customConfig);
    // });
  }
  var handle = setInterval(function () {
    if (_self.bot.options.accessToken) {
      _self.unlockBot();
      _self.getTabFacetList(_self.API.tabFacetUrl, "GET");
      _self.getSortableFacetList(_self.API.sortableFacetsUrl, 'GET');
      _self.getSearchResultsConfig(_self.API.searchResultsViewConfigURL, "GET");
      _self.saveCustomizationConfig();
      clearInterval(handle);
      handle = 0;
    }
  }, 1000);
};
FindlySDK.prototype.addConversationBox = function (config) {
  var _self = this;
  $("#" + config.container).addClass("conversation-box");
  var conversationBox =
    '<input type="text" class="conversation-box-input" placeholder="Type message..." id="sa-conversation-box"/>';
  $("#" + config.container)
    .empty()
    .append(conversationBox);
  if (config.classes) {
    $(".conversation-box-input").addClass(config.classes);
  }
  var ccBox = "#sa-conversation-box";
  $(ccBox)
    .off("keydown")
    .on("keydown", function (event) {
      var code = event.keyCode || event.which;
      if (code === "13" || code === 13) {
        var msg = $(ccBox).val();
        _self.sendMessageToSearch("user-conversation");
        if (_self.config.viaSocket) {
          _self.sendMessage(msg);
        } else {
          _self.bindLiveDataToChat();
        }
        $(ccBox).val("");

        if ($("body").hasClass("top-down")) {
          _self.vars.selectedFacetFromSearch = "all results";
          _self.pubSub.publish("facet-selected", {
            selectedFacet: "all results",
          });
          $("#searchChatContainer .finalResults").addClass("hide");
        }
      }
    });
};
FindlySDK.prototype.addSearchText = function (config) {
  var _self = this;
  if ($("body").hasClass("demo")) {
    $("body").removeClass("demo");
    $("body").addClass("top-down");
    $("body").addClass("sdk-top-down-interface");
  } else {
    $("body").addClass("top-down");
    $("body").addClass("sdk-top-down-interface");
  }
  _self.customSearchResult = false;
  window.koreWidgetSDKInstance = _self;

  if (config && config.autoSuggest === true) {
    _self.pubSub.unsubscribe("sa-auto-suggest");
    _self.pubSub.subscribe("sa-auto-suggest", (msg, data) => {
      _self.getSuggestion(data);
    });
  }

  if (config && config.showRecentSearches === true) {
    _self.enableRecent();
  }

  if (config && config.focusHandler) {
    _self.pubSub.unsubscribe("sa-search-focus");
    _self.pubSub.subscribe("sa-search-focus", (data) => {
      config.focusHandler();
      if (!$(".search-top-down").val()) {
        if ($("#search").is(":focus")) {
          if ($("#greeting-msg-top-down").length) {
            $("#greeting-msg-top-down").hide();
          }

          if ($("#auto-query-box").find(".suggestion-box").length) {
            $(".suggestion-box").remove();
          }
          $(".top-down-suggestion").val("");
          $("#live-search-result-box").hide();
        } else {
          $("#frequently-searched-box").hide();
        }
      }
    });
  }

  var dataHTML = $(_self.getSearchControl()).tmplProxy(config);
  if (config.microphone) {
    $(".notRecordingMicrophone")
      .off("click")
      .on("click", function (event) {
        getSIDToken();
      });
    $(".recordingMicrophone")
      .off("click")
      .on("click", function (event) {
        stop();
      });
  }
  _self.bindPlaceholderStyle(config);
  _self.bindContextVariable();
  _self.bindSearchAccordion();
  _self.bindFeedbackEvent();
  // debugger;
  var container = $("#" + config.container);
  $(container).append(dataHTML);
  if (!$("body").hasClass("top-down")) {
    _self.bindPerfectScroll(dataHTML, ".search-body", null, "searchBody");
    _self.bindPerfectScroll(
      dataHTML,
      "#searchChatContainer",
      null,
      "searchChatContainer"
    );
  }

  if (config.showGreeting) {
    _self.showGreetingMsg = true;
    _self.vars.searchObject.clearGreetingTimeOut = setTimeout(function () {
      var greetingMsg = $(_self.getGreetingMsgTemplate()).tmplProxy(config);
      $(".greetingMsg").removeClass("hide");
      $(".greetingMsg").html(greetingMsg);
      if ($(".search-container").find(".greetingMsg").length) {
        $(".greetingMsg").parent().css("box-shadow", "none");
      }
      _self.bindCloseGreeting();
    }, 1000);
  }
  $(".cancel-search")
    .off("click")
    .on("click", function (event) {
      $("#search").val("");
      $("#suggestion").val("");
      $(".search-top-down").val("");
      $(".top-down-suggestion").val("");
      if ($(".topdown-search-main-container").length) {
        $(".cancel-search").hide();
        if ($('body').hasClass('search-executed')) {
          $('.search-icon-btn').show();
        }
        $("#live-search-result-box").hide();
        //$('#frequently-searched-box').show();
        $("#search").trigger("click");
      }
    });
  _self.searchEventBinding(dataHTML, "search-container", {}, config);
};
