var searchConfigurationCopy = {};

FindlySDK.prototype.mapSearchConfiguration = function (searchConfig) {
  var _self = this;
  var searchConfiguration = {};
  if (searchConfig && Object.values(searchConfig).length) {
    if (searchConfig.widgetConfig) {
      searchConfiguration.buttonBorderColor =
        searchConfig.widgetConfig.buttonBorderColor;
      searchConfiguration.buttonFillColor =
        searchConfig.widgetConfig.buttonFillColor;
      searchConfiguration.buttonText = searchConfig.widgetConfig.buttonText;
      searchConfiguration.buttonTextColor =
        searchConfig.widgetConfig.buttonTextColor;
      searchConfiguration.searchBarBorderColor =
        searchConfig.widgetConfig.searchBarBorderColor;
      searchConfiguration.searchBarFillColor =
        searchConfig.widgetConfig.searchBarFillColor;
      searchConfiguration.searchBarPlaceholderText =
        searchConfig.widgetConfig.searchBarPlaceholderText;
      searchConfiguration.searchBarPlaceholderTextColor =
        searchConfig.widgetConfig.searchBarPlaceholderTextColor;
      searchConfiguration.searchButtonEnabled =
        searchConfig.widgetConfig.searchButtonEnabled;
      searchConfiguration.buttonPlacementPosition =
        searchConfig.widgetConfig.buttonPlacementPosition;
    } else {
      searchConfiguration.buttonBorderColor = "#e5e8ec";
      searchConfiguration.buttonFillColor = "#0D6EFD";
      searchConfiguration.buttonText = "Go";
      searchConfiguration.buttonTextColor = "#fff";
      searchConfiguration.searchBarBorderColor = "#e5e8ec";
      searchConfiguration.searchBarFillColor = "#00ffff00";
      searchConfiguration.searchBarPlaceholderText = "Ask anything";
      searchConfiguration.searchBarPlaceholderTextColor = "#21202447";
      searchConfiguration.searchButtonEnabled = true;
      searchConfiguration.buttonPlacementPosition = "inside";
    }
    if (
      searchConfig.widgetConfig.searchBarIcon &&
      searchConfig.widgetConfig.searchBarIcon.length
    ) {
      searchConfiguration.searchBarIcon =
        searchConfig.widgetConfig.searchBarIcon;
    } else {
      searchConfiguration.searchBarIcon =
        searchConfig.experienceConfig.searchBarPosition == "top"
          ? "data:image/png;base64,"
          : "data:image/png;base64,";
    }

    if (searchConfig.interactionsConfig) {
      searchConfiguration.welcomeMsg =
        searchConfig.interactionsConfig.welcomeMsg;
      searchConfiguration.welcomeMsgColor =
        searchConfig.interactionsConfig.welcomeMsgColor || "#3C4043";
      searchConfiguration.welcomeMsgFillColor =
        searchConfig.interactionsConfig.welcomeMsgFillColor || "#F8F9FA";
      searchConfiguration.defaultStatus =
        searchConfig.interactionsConfig.defaultStatus || "searchBar";
      searchConfiguration.showSearchesEnabled =
        searchConfig.interactionsConfig.showSearchesEnabled;
      searchConfiguration.showSearches =
        searchConfig.interactionsConfig.showSearches;
      searchConfiguration.autocompleteOpt =
        searchConfig.interactionsConfig.autocompleteOpt;
      searchConfiguration.querySuggestionsLimit =
        searchConfig.interactionsConfig.querySuggestionsLimit;
      searchConfiguration.liveSearchResultsLimit =
        searchConfig.interactionsConfig.liveSearchResultsLimit;
      searchConfiguration.feedbackExperience =
        searchConfig.interactionsConfig.feedbackExperience;
        _self.vars.feedBackExperience = searchConfig.interactionsConfig.feedbackExperience.queryLevel;
    } else {
      searchConfiguration.welcomeMsg = "Hello! How can I help you today?";
      searchConfiguration.welcomeMsgFillColor = "#3C4043";
      searchConfiguration.defaultStatus || "searchBar";
      searchConfiguration.welcomeMsgFillColor = "#F8F9FA";
      searchConfiguration.showSearchesEnabled = "#161928";
      searchConfiguration.showSearches = "recent";
      searchConfiguration.autocompleteOpt = true;
      searchConfiguration.querySuggestionsLimit = 2;
      searchConfiguration.liveSearchResultsLimit = 2;
      searchConfiguration.feedbackExperience = {
        resultLevel: true,
        queryLevel: false,
      };
    }

    if (
      searchConfig.interactionsConfig.welcomeMsgEmoji &&
      searchConfig.interactionsConfig.welcomeMsgEmoji.length
    ) {
      searchConfiguration.welcomeMsgEmoji =
        searchConfig.interactionsConfig.welcomeMsgEmoji;
    } else {
      searchConfiguration.welcomeMsgEmoji =
        "data:image/png;base64,";
    }
    if (searchConfig.config) {
      searchConfiguration.botConfig = searchConfig.config;
    }
  } else {
    searchConfiguration = {
      searchBarIcon:
        "data:image/png;base64,",
      buttonBorderColor: "#e5e8ec",
      buttonFillColor: "#0D6EFD",
      buttonText: "Go",
      buttonTextColor: "#fff",
      searchBarBorderColor: "#e5e8ec",
      searchBarFillColor: "#00ffff00",
      searchBarPlaceholderText: "Ask anything",
      searchBarPlaceholderTextColor: "#21202447",
      searchButtonEnabled: true,
      buttonPlacementPosition: "inside",
      welcomeMsg: "Hello! How can I help you today?",
      welcomeMsgEmoji:
        "data:image/png;base64,",
      showSearchesEnabled: "#161928",
      showSearches: "recent",
      autocompleteOpt: true,
      querySuggestionsLimit: 2,
      liveSearchResultsLimit: 2,
      feedbackExperience: { resultLevel: true, queryLevel: false },
    };
  }
  //default showsearches as recent
  searchConfiguration.showSearches = "recent";
  searchConfigurationCopy = searchConfiguration;
  return searchConfiguration;
};

