FindlySDK.prototype.sendMessage = function (chatInput, renderMsg, msgObject, isbotActionTrigger) {
  var _self = this;
  if (!$(".topdown-search-main-container").length) {
    $("#search").val("");
    $(".bottom-up-suggestion").val("");
    $(".bottom-up-search").val("");
  } else {
    $("#suggestion").val("");
    $(".top-down-suggestion").val("");
    if(!isbotActionTrigger){
      _self.appendTextToSearchContainer('user', chatInput);
    }
  }
  $("#frequently-searched-box").hide();
  if(!$("#conversation-container").is(":visible")){
    $("#live-search-result-box").hide();
  }
  

  // debugger;

  var currentDate = new Date();
  var dateTime =
    currentDate.getDate() +
    "/" +
    (currentDate.getMonth() + 1) +
    "/" +
    currentDate.getFullYear() +
    ", " +
    currentDate.getHours() +
    ":" +
    currentDate.getMinutes() +
    ":" +
    currentDate.getSeconds();

  var clientMessageId = new Date().getTime();
  var msgData = {};
  var fileUploaderCounter = 0;
  var attachmentInfo;
  //to send \n to server for new lines
  //chatInput.html(chatInput.html().replaceAll("<br>", "\n"));
  if (false && attachmentInfo && Object.keys(attachmentInfo).length) {
    msgData = {
      type: "currentUser",
      message: [
        {
          type: "text",
          cInfo: {
            body: chatInput.text(),
            attachments: [attachmentInfo],
          },
          clientMessageId: clientMessageId,
        },
      ],
      createdOn: clientMessageId,
    };
    $(".attachment").html("");
    $(".kore-chat-window").removeClass("kore-chat-attachment");
    document.getElementById("captureAttachmnts").value = "";
  } else {
    attachmentInfo = {};
    msgData = {
      type: "currentUser",
      message: [
        {
          type: "text",
          cInfo: { body: chatInput },
          clientMessageId: clientMessageId,
        },
      ],
      createdOn: clientMessageId,
    };
  }

  var messageToBot = {};
  messageToBot["clientMessageId"] = clientMessageId;
  if (
    Object.keys(attachmentInfo).length > 0 &&
    chatInput.text().trim().length
  ) {
    messageToBot["message"] = {
      body: chatInput.text().trim(),
      attachments: [attachmentInfo],
    };
  } else if (Object.keys(attachmentInfo).length > 0) {
    messageToBot["message"] = { attachments: [attachmentInfo] };
  } else {
    messageToBot["message"] = { body: chatInput };
  }
  messageToBot["resourceid"] = "/bot.message";



  messageToBot["location"] = _self.vars.locationObject.location;
  messageToBot["timeDateDay"] = dateTime;
  messageToBot["currentPage"] = window.location.href;
  messageToBot["country"] = _self.vars.locationObject.country;
  if (_self.vars.customizeView) {
    messageToBot["customize"] = true;
    messageToBot["isDev"] = true;
  }
  if (_self.bot.options) {
    messageToBot["client"] = _self.bot.options.client || "sdk";
    messageToBot["botInfo"] = {};
    messageToBot["botInfo"].chatBot = _self.bot.options.botInfo.chatBot;
    messageToBot["botInfo"].taskBotId = _self.bot.options.botInfo.taskBotId;
    messageToBot["botInfo"].customData = {};
  }
  if (_self.config.botOptions.searchIndexID) {
    messageToBot["searchIndexId"] = _self.config.botOptions.searchIndexID;
  }
  if (renderMsg && typeof renderMsg === "string") {
    messageToBot["message"].renderMsg = renderMsg;
  }
  if (msgObject && msgObject.customdata) {
    messageToBot["message"].customdata = msgObject.customdata;
  }

  if (msgObject && msgObject.nlmeta) {
    messageToBot["message"].nlmeta = msgObject.nlmeta;
  }

  if (msgObject && msgObject.linkedBotNLMeta) {
    _self.bot.options.botInfo.linkedBotNLMeta = msgObject.linkedBotNLMeta;
  } else if (_self.bot.options.botInfo.linkedBotNLMeta) {
    delete _self.bot.options.botInfo.linkedBotNLMeta;
  }
  //advanced result template websocket payload added//
  messageToBot["tabConfig"] = {
    // required
    filter: {},
  };
  messageToBot["resultGroups"] = {
    // required
    filter: {},
  };
  messageToBot["defaultGroups"] = {
    // required
    filter: {},
  };
  messageToBot["interface"] = $("body").hasClass("top-down")
    ? "fullSearch"
    : "conversationalSearch";

    messageToBot["maxNumOfResults"] = (messageToBot["interface"]=="conversationalSearch")? 2 : 5;
  //payload end//
  if (!$("body").hasClass("demo")) {
    messageToBot.indexPipelineId = _self.API.indexpipelineId;
    messageToBot.queryPipelineId = _self.API.pipelineId;
  }

  if (_self.bot.options) {
    _self.bot.options.botInfo.linkedBotCustomData = _self.bot.options.botInfo
      .linkedBotCustomData
      ? _self.bot.options.botInfo.linkedBotCustomData
      : { linkedBot: {} };
    if (!_self.bot.options.botInfo.customData) {
      _self.bot.options.botInfo.customData = {};
    }
    if (!_self.vars.isCustomDataInitialize) {
      _self.vars.userContextData =
        _self.bot.options.botInfo.customData["userContext"] || {};
      _self.vars.isCustomDataInitialize = true;
    }
    var contextObj = $("#contextjsonfield").val();
    if (contextObj) {
      contextObj.trim();
      if (contextObj) {
        try {
          contextObj = JSON.parse(contextObj);
        } catch (error) {
          contextObj = {};
        }
      }
      _self.bot.options.botInfo.customData["userContext"] = contextObj;
    } else {
      if (!window.localStorage.getItem("userName")) {
        _self.customData;
        _self.bot.options.botInfo.customData["userContext"] =
          _self.vars.userContextData || {};
      } else {
        _self.bot.options.botInfo.customData["userContext"] = {
          user_name: window.localStorage.getItem("userName"),
          location: window.localStorage.getItem("userLocation"),
          gender: window.localStorage.getItem("gender"),
          user_age: window.localStorage.getItem("userAge"),
        };
      }
    }
  }

  attachmentInfo = {};
  websockeRrefreshed = false;
  _self.checkWbInitialized(messageToBot, clientMessageId);

  // if (_self.isDev == false) {
  _self.resetPingMessage();
  _self.resetSocketDisconnection();
  // }
};
FindlySDK.prototype.checkWbInitialized = function (
  messageToBot,
  clientMessageId
) {
  var _self = this;
  if (websockeRrefreshed) {
    return;
  }
  var sendMsgTimeOut = _self.vars.isSocketReInitialize ? 2000 : 0;
  // $('.typingIndicatorContent').css('display', 'block');
  _self.showTypingIndicator();
  setTimeout(() => {
    if (_self.bot.initialized) {
      _self.vars.isSocketReInitialize = false;
      _self.bot.sendMessage(messageToBot, function messageSent(err) {
        if (err && err.message) {
          setTimeout(function () {
            $("#msg_" + clientMessageId)
              .find(".messageBubble")
              .append(
                '<div class="errorMsg">Send Failed. Please resend.</div>'
              );
            $(".typingIndicatorContent").css("display", "none");
          }, 350);
        }
      });
    } else {
      _self.vars.isSocketReInitialize = true;
      _self.checkWbInitialized(messageToBot, clientMessageId);
    }
  }, sendMsgTimeOut);
};
