FindlySDK.prototype.resolveUrl = function (toResolveUrl, values, deleteProp) {
  var _regExToParamName = /\:([a-zA-Z]+)/g;
  return toResolveUrl.replace(_regExToParamName, function (matchStr, valName) {
    var r = values[valName];

    if (typeof r !== "undefined" && typeof r !== null) {
      if (deleteProp) {
        delete values[valName];
      }

      return r;
    }

    return matchStr;
  });
};

FindlySDK.prototype.getResolveMeeting = function (obj) {
  var _self = this;

  var items = [];

  if (obj && obj.elements && obj.elements.length) {
    for (i = 0; i < obj.elements.length; i++) {
      var temp = [];
      var actionsArr = obj.elements[i].actions;

      for (var k = 0; k < actionsArr.length; k++) {
        var actionObj = actionsArr[k];

        if (actionObj.type !== "dial") {
          temp.push(actionObj);
        }
      }

      obj.elements[i].actions = temp;
      var ele = Object.assign({}, obj.elements[i]);

      var slots = _self.getMeetingSlot(ele.data.duration);

      for (j = 0; j < slots.length; j++) {
        var today = new Date();
        var localStarttime = new Date(slots[j].start);

        if (
          ((today.getFullYear() <= localStarttime.getFullYear() &&
            today.getMonth() <= localStarttime.getMonth() &&
            today.getDate() <= localStarttime.getDate()) ||
            today.getTime() <= slots[j].start) &&
          slots[j].end <= obj.cursor.end
        ) {
          ele.data.duration = slots[j];
          var startTime = new Date(slots[j].start);
          var endTime = new Date(slots[j].end);

          if (slots.length === 1) {
            if (ele.data.isAllDay) {
              ele.day = "All Day";
              ele.break = true;
              ele.localDay = {
                intial: "All Day",
              };
              items.push(_self.cloneMessage1(ele));
            } else if (
              startTime.getHours() === 0 &&
              startTime.getMinutes() === 0 &&
              endTime.getHours() === 23 &&
              endTime.getMinutes() === 59
            ) {
              ele.day = "All Day";
              ele.break = true;
              ele.localDay = {
                intial: "All Day",
              };
              items.push(_self.cloneMessage1(ele));
            } else {
              items.push(_self.cloneMessage1(ele));
            }
          } else {
            var off = j + 1;

            if (
              startTime.getHours() === 0 &&
              startTime.getMinutes() === 0 &&
              endTime.getHours() === 23 &&
              endTime.getMinutes() === 59
            ) {
              ele.day = "All Day";
              ele.break = true;

              if (off === slots.length) {
                ele.localDay = {
                  intial: "All Day",
                  last: " (Day " + off + "/" + slots.length + ")",
                };
              } else {
                ele.localDay = {
                  intial: "All Day",
                  last: " (Day " + off + "/" + slots.length + ")",
                };
              }
            } else if (off !== slots.length) {
              ele.day =
                "From" +
                getTime(startTime) +
                " Day (" +
                off +
                "/" +
                slots.length +
                ")";
              ele.break = true;
              ele.localDay = {
                intial: "From",
                time: getTime(startTime),
                last: " (Day " + off + "/" + slots.length + ")",
              };
            } else if (off === slots.length) {
              ele.day =
                "Till" +
                getTime(endTime) +
                " Day (" +
                off +
                "/" +
                slots.length +
                ")";
              ele.break = true;
              ele.localDay = {
                intial: "Till",
                time: getTime(endTime),
                last: " (Day " + off + "/" + slots.length + ")",
              };
            }

            items.push(_self.cloneMessage1(ele));
          }
        }
      }
    }

    items.sort(_self.compare);
    return items;
  } else {
    return items;
  }
};

FindlySDK.prototype.filterTabs = function (parentId, subpanelId, filterId) {
  var _self = this;

  $(parentId + " #" + subpanelId + " ." + subpanelId + "_content").hide(); // hiding the content panel

  $(parentId + " #" + subpanelId + " #" + filterId + "_content").show(); // showing the content panel

  $(parentId + " #" + subpanelId + " .filterCntr li")
    .addClass("unActive")
    .removeClass("active"); // removing active class from all and adding unactive class to all

  $(parentId + " #" + subpanelId + " #" + filterId)
    .addClass("active")
    .removeClass("unActive"); // add active in current tab and remove unactive

  _self.resetTask();
};

