FindlySDK.prototype.bindSearchContainerViewHadler = function () {
  var _self = this;
  _self.pubSub.subscribe("sa-handel-chat-container-view", (msg, data) => {
    let parentChatContainer = $("#searchChatContainer");
    if (parentChatContainer.children().length > 2) {
      if ($(".search-container").hasClass("no-history")) {
        $(".search-container").removeClass("no-history");
      }
    } else {
      if (!$(".search-container").hasClass("no-history")) {
        _self.changeSearchContainerBackground();
        // $('.search-container').addClass('no-history');
      }
    }
  });

  _self.pubSub.subscribe("sa-handel-go-button", (msg, data) => {
    var isGoButtonDisabled = false;
    if (
      !($("body").hasClass("top-down")
        ? $(".search-top-down").val()
        : $(".bottom-up-search").val())
    ) {
      isGoButtonDisabled = true;
    } else {
      isGoButtonDisabled = false;
    }
    var goBtn = $(".search-button");
    for (let i = 0; i < goBtn.length; i++) {
      goBtn[i].disabled = isGoButtonDisabled;
    }
  });

  _self.pubSub.subscribe("sa-update-search-customozation", (msg, data) => {
    _self.invokeSearch();
  });

  _self.pubSub.subscribe("sa-handle-customize-option-view", (msg, data) => {
    if ($(".sdk-results-customize-icon").length) {
      $(".sdk-results-customize-icon").css("display", "none");
      $(".sdk-results-customize-icon").last().css("display", "block");
    }
    $(".sdk-results-customize-icon")
      .off("click")
      .on("click", function (e) {
        _self.vars.customizeView = true;
        $(".show-all-results").click();
        $("#searchChatContainer")
          .off("scroll")
          .on("scroll", function (event) {
            $(".query_analytics_content").hide();
            $(".typingIndicatorContent").css("display", "none");
          });
      });
  });

  _self.pubSub.subscribe("sa-handle-retry-qery-view", (msg, data) => {
    var _self = this;
    $(".sdk-query-retry-icon")
      .off("click")
      .on("click", function (e) {
        let searchQuery = $(e.target)
          .closest(".sdk-query-retry-icon")
          .attr("searchQuery");
        if (searchQuery && searchQuery.length) {
          var scrollHeight =
            $("#searchChatContainer").scrollTop() +
            $("#searchChatContainer").prop("scrollHeight");
          $("#searchChatContainer").animate({ scrollTop: scrollHeight }, 500);
          _self.sendMessage(searchQuery);
        } else {
          var scrollHeight =
            $("#searchChatContainer").scrollTop() +
            $("#searchChatContainer").prop("scrollHeight");
          $("#searchChatContainer").animate({ scrollTop: scrollHeight }, 500);
          _self.sendMessage(_self.vars.searchObject.searchText);
        }
      });
  });

  $(".refresh-sdk")
    .off("click")
    .on("click", function (e) {
      $("#show-all-results-container").hide();
      $(".search-container").removeClass("bottom-up-results-showing");
      websockeRrefreshed = true;
      clearTimeout(indicatorTimer);
      $(".typingIndicatorContent").css("display", "none");
      $(".search-container").removeClass("active");
        $('.search-container').css({ 'height': 'auto' });
        _self.vars.selectedFacetFromSearch = "all results";
      var responseObject = {
        type: "refreshSearchContainer",
        data: false,
        query: _self.vars.searchObject.searchText,
        bottomUp: true,
      };
      _self.parentEvent(responseObject);
    });
  $(".kore-search-container-close-icon")
    .off("click")
    .on("click", function (e) {
      $("#show-all-results-container").hide();
      $(".search-container").removeClass("bottom-up-results-showing");
      websockeRrefreshed = true;
      clearTimeout(indicatorTimer);
      $(".typingIndicatorContent").css("display", "none");

      $(".search-container").removeClass("active");
        $('.search-container').css({ 'height': 'auto' });
        _self.vars.selectedFacetFromSearch = "all results";
      var responseObject = {
        type: "closeSearchContainer",
        data: false,
        query: _self.vars.searchObject.searchText,
        bottomUp: true,
      };
      _self.parentEvent(responseObject);
    });
  if (!$("body").hasClass("top-down")) {
    // if (searchConfigurationCopy.searchBarPlaceholderTextColor && searchConfigurationCopy.searchBarPlaceholderTextColor.length) {
    //   $("body").append("<style>#search::placeholder {color:" + searchConfigurationCopy.searchBarPlaceholderTextColor + "!important;}</style>")
    // }
  }
};


