FindlySDK.prototype.saveCustomizationConfig = function () {
  var _self = this;
  // for saving the Search Customizatios
  _self.pubSub.subscribe("sa-save-search-customization-config", (msg, data) => {
    if (data && data.length) {
      data.forEach((interface1) => {
        if (interface1.interface === "fullSearch") {
          if (interface1.facetsSetting) {
            _self.vars.filterConfiguration = {
              aligned: interface1.facetsSetting.aligned,
              isEnabled: interface1.facetsSetting.enabled,
            };
          } else {
            _self.vars.filterConfiguration = {
              aligned: "left",
              isEnabled: true,
            };
          }

        }
      });
    }
  });
};

