FindlySDK.prototype.showAllResults = function () {
  var _self = this;
  

  _self.pubSub.unsubscribe("sa-search-full-results");
  _self.pubSub.subscribe("sa-search-full-results", (msg, data) => {
    // _self.vars.scrollPageNumber = 0;
    if (!_self.vars.isFilterModified) {
      _self.autoSelectFacetFilter();
    }
    _self.vars["selectedFiltersRadio"] = [];
    var facetCount = {};
    var facetData = [];
    var count = 0;
    var view = "preview"; 
    if (data && data.facetData) {
      facetData = data.facetData;
    }
    if (
      _self.vars &&
      _self.vars.selectedFiltersArr &&
      _self.vars.selectedFiltersArr.length
    ) {
      count = _self.vars.selectedFiltersArr.length;
    }
    if (data && data.container) {
      var isFilterEnabled = _self.vars.filterConfiguration.isEnabled;
      if (isFilterEnabled || isFilterEnabled == false) {
        isFilterEnabled = isFilterEnabled;
      } else {
        isFilterEnabled = false;
      }

      _self.getMergedData(_self.vars.resultSettings, data.responseData, 'isFullResults').then((res) => {
        var devMode = _self.isDev ? true : false;
        var viewType = _self.vars.customizeView ? "Customize" : "Preview";
        var msgData = {
          message: [{
            component: {
              type: 'template',
              payload: {
                template_type: "fullSearchResultsTemplate",
                facets: _self.vars.tabsList,
                count: count,
                view: view,
                isDev: _self.isDev,
                isFilterEnabled: isFilterEnabled,
                devMode: devMode,
                viewType: viewType,
                facetPosition: _self.vars.filterConfiguration.aligned,
                filterFacetData: facetData,
                groupData: _self.vars.mergedData,
                searchConfigurationCopy: searchConfigurationCopy,
                sortableFacetList: _self.vars.sortableFacetList || [],
                displaySortable: _self.vars.displaySortable,
                displayFeedback:_self.vars.feedBackExperience,
                feedbackData: _self.vars.feedBackExperience? _self.vars.feedBackType:null
              }
            }
          }]
        }
        var showAllHTML = _self.customTemplateObj.renderMessage(msgData);
        $(data.container).empty().append(showAllHTML);
        var facetObj = {};
        facetObj["position"] = _self.vars.filterConfiguration.aligned; // "left";
        facetObj["show"] = false;
        // if(facetObj['position'] == "top"){
        //   facetObj['show'] = true;
        // }
        _self.vars["facetObjectGlobal"] = facetObj;
        // _self.facetReset(facetObj, facetData);
        _self.bindShowAllResultsTrigger(showAllHTML, facetData, data);
      });
     
    }
    $('.queryString').html(_self.vars.searchObject.searchText);
    $("#show-all-results-container").css("display", "block");
    $("#searchChatContainer").removeClass("bgfocus");
    $(".search-body").addClass("hide");

    var resultRanking = $(_self.fullResultRanking()).tmpl({
      data: data.dataObj,
      facetPosition: _self.vars.filterConfiguration.aligned,
    });
    $("#resultRankingId").append(resultRanking);
    // setTimeout( () => {
    //   _self.checkBoostAndLowerTimes();
    // }, 400);
    _self.bindAllResultRankingOperations();
    // _self.bindShowAllResultsTrigger(showAllHTML, facetData, data);
    setTimeout(() => {
      if (!$("body").hasClass("top-down")) {
        if (_self.isDev) {
          if (_self.vars.customizeView) {
            $("#viewTypePreview").removeClass("nav-link-item-active");
            $("#viewTypeCustomize").addClass("nav-link-item-active");
          } else {
            $(".query-analytics-control-container").hide();
            $("#viewTypePreview").addClass("nav-link-item-active");
            $("#viewTypeCustomize").removeClass("nav-link-item-active");
          }
        }
      }
    }, 100);
  });
};

FindlySDK.prototype.bindCustomizeAction = function () {
  var _self = this;
  if (!$("body").hasClass("top-down")) {
    if (_self.isDev) {
      if (_self.vars.customizeView) {
        $("#viewTypePreview").removeClass("nav-link-item-active");
        $("#viewTypeCustomize").addClass("nav-link-item-active");
        $(".data-body-sec").css("height", "calc(100vh - 260px)");
      } else {
        $(".query-analytics-control-container").hide();
        $("#viewTypePreview").addClass("nav-link-item-active");
        $("#viewTypeCustomize").removeClass("nav-link-item-active");
        if (
          _self.vars.selectedFiltersArr &&
          _self.vars.selectedFiltersArr.length > 0
        ) {
          $(".data-body-sec").css("height", "calc(100vh - 225px)");
        } else {
          $(".data-body-sec").css("height", "calc(100vh - 195px)");
        }
      }

      $(".sdk-customize-nav")
        .off("click")
        .on("click", function (event) {
          event.preventDefault();
          event.stopImmediatePropagation();

          var navLinks = document.getElementsByClassName(
            "custom-header-nav-link-item"
          );

          if (
            _self.config.viaSocket &&
            _self.vars.resultRankingActionPerformed == true
          ) {
            var scrollHeight =
              $("#searchChatContainer").scrollTop() +
              $("#searchChatContainer").prop("scrollHeight");
            $("#searchChatContainer").animate({ scrollTop: scrollHeight }, 500);
            if (!$("body").hasClass("top-down")) {
              _self.sendMessage(_self.vars.searchObject.searchText);
            }
          }

          for (var i = 0; i < navLinks.length; i++) {
            navLinks[i].className = navLinks[i].className.replace(
              " nav-link-item-active",
              ""
            );
          }
          event.currentTarget.className += " nav-link-item-active";
          if ($("body").hasClass("top-down")) {
            var searchData;
          }
          if ($(searchData).find(".tasks-wrp .faqs-shadow")) {
            $(searchData)
              .find(".tasks-wrp .faqs-shadow")
              .find(".accordion.acc-active")
              .trigger("click");
          }
          if (_self.vars.showingMatchedResults == true) {
            if ($(event.currentTarget).attr("id") == "viewTypeCustomize") {
              _self.vars.customizeView = true;
              // $(".custom-insights-control-container").show();

              $(".query-analytics-control-container").show(); // Temporary modification, made as per FLY-1012

              $(".tasks-wrp").sortable();
              $(".tasks-wrp").sortable("option", "disabled", false);
              $(".tasks-wrp").disableSelection();

              $(".faqs-shadow").addClass("custom-faqs-shadow");
              $(".faqs-wrp-content").addClass("custom-faqs-wrp-content");

              $(".faqs-bottom-actions").addClass("custom-faqs-bottom-actions");

              $(".image-url-sec").css("display", "none");
              $(".faqs-bottom-actions").css("display", "table");

              $(".divfeedback").css("display", "none");
              $("#searchChatContainer")
                .off("scroll")
                .on("scroll", function (event) {
                  $(".query_analytics_content").hide();
                });
            } else {
              _self.vars.customizeView = false;

              // $(".custom-insights-control-container").hide();

              $(".query-analytics-control-container").hide(); // Temporary modification, made as per FLY-1012

              $(".faqs-shadow").removeClass("custom-faqs-shadow");
              $(".faqs-wrp-content").removeClass("custom-faqs-wrp-content");

              $(".faqs-bottom-actions").removeClass(
                "custom-faqs-bottom-actions"
              );

              $(".tasks-wrp").sortable("disable");
              $(".image-url-sec").css("display", "table-cell");
              $(".faqs-bottom-actions").css("display", "none");

              // $(".divfeedback").css('display', 'block');

              $(".notification-div").hide();
            }
            /*_self.getFrequentlySearched(url, 'POST', JSON.stringify(payload)).then(function (response) {
              _self.handleSearchRes(response);
              });*/
          } else {
            if ($(event.currentTarget).attr("id") == "viewTypeCustomize") {
              _self.vars.customizeView = true;
            } else {
              _self.vars.customizeView = false;
            }
          }
          if (!$("body").hasClass("top-down")) {
            _self.pubSub.publish("sa-update-search-customozation");
          }
          $(".structured-data-bottom-actions").css("display", "none");
          $(".structured-data-wrp-content").removeClass(
            "custom-faqs-wrp-content"
          );
          $(".moreStructredData").addClass("display-none");
        });
    }
  }
};

FindlySDK.prototype.seeAllResultsInifiteScroll = function () {
  // call the required API
  this.invokeSearch();
};

FindlySDK.prototype.invokeSpecificSearch = function (selectedFacet) {
  var _self = this;
  var url = _self.API.searchUrl;
  var currentDate = new Date();
  var dateTime =
    currentDate.getDate() +
    "/" +
    (currentDate.getMonth() + 1) +
    "/" +
    currentDate.getFullYear() +
    ", " +
    currentDate.getHours() +
    ":" +
    currentDate.getMinutes() +
    ":" +
    currentDate.getSeconds();
  var selectedTopFacet = {};

  var payload = {
    query: _self.vars.searchObject.searchText,
    // "maxNumOfResults": 9,
    maxNumOfResults: 5,
    userId: _self.API.uuid,
    streamId: _self.API.streamId,
    lang: "en",
    // "isDev": true,
    isDev: _self.isDev,
    filters: [],
    searchRequestId: _self.vars.requestId,
  };
  if (_self.vars.isFilterModified) {
    payload['isFilterModified'] = true;
  }
  if (_self.isDev) {
    payload["customize"] = _self.vars.customizeView;
  }
  if (_self.vars.displaySortable) {
    payload['sortableFacets'] = [_self.vars.displaySortable];
  }
  var contentTypeFilter = {
    fieldName: "sys_content_type",
    name: "facetContentType",
    subtype: "value",
    multiselect: false,
    buckets: [],
  };

  if (_self.vars.filterObject.length > 0) {
    payload.filters = JSON.parse(JSON.stringify(_self.vars.filterObject));
  }

  if (selectedFacet !== "all results") {
    var tabConfig = {
      filter: {
        fieldName: _self.vars.tabFacetFieldName,
        facetValue: [selectedFacet],
      },
    };
    payload.tabConfig = tabConfig;
    if (Object.values(selectedTopFacet).length) {
      if (!payload.filters || !payload.filters.length) {
        payload.filters = [];
      }
      payload.filters.push(selectedTopFacet);
    }
  }

  if (_self.vars.filterObject && _self.vars.filterObject.length) {
    if (!_self.vars.isPriorfiltersApplied) {
      // _self.vars.scrollPageNumber = 0;
    }
  }

  _self
    .getFrequentlySearched(url, "POST", JSON.stringify(payload))
    .then(function (res) {
      $("#loaderDIV").hide();
      // $('.kore-sdk-pagination-div').show();
      if (_self.vars.searchObject && _self.vars.searchObject.searchText) {
        var responseObject = {
          type: "fullResult",
          data: true,
          query: _self.vars.searchObject.searchText,
        };
        _self.parentEvent(responseObject);
      }

      $(".custom-insights-control-container").hide();
      var faqs = [],
        web = [],
        tasks = [],
        files = [],
        facets = [],
        data = {},
        searchFacets = [];

      if (res.template) {
        searchFacets = res.template.searchFacets;
        _self.vars.searchObject.liveData = {
          facets: _self.vars.tabsList,
          // files: files,
          data: data,
          originalQuery: res.template.originalQuery || "",
          // searchFacets: searchFacets,
        };
        var dataObj = _self.vars.searchObject.liveData;
        var facetdata = _self.vars.searchFacetFilters;
        $(".data-body-sec").scrollTop(0);
        // _self.calculatePageNumber(selectedFacet, dataObj);
        if (!$("body").hasClass("top-down")) {
          // _self.handlePaginationUI(selectedFacet, dataObj);

          _self.pubSub.publish("sa-action-full-search", {
            container: "#actions-full-search-container",
            isFullResults: true,
            selectedFacet: selectedFacet,
            isLiveSearch: false,
            isSearch: false,
            dataObj,
          });
        }
        res = res.template;
        facets = [];
        var totalResultsCount = 0;
        if (
          res.tabFacet &&
          res.tabFacet.buckets &&
          res.tabFacet.buckets.length
        ) {
          res.tabFacet.buckets.forEach((tab) => {
            totalResultsCount = totalResultsCount + tab.doc_count;
            if (!tab.name) {
              tab.name = tab.key;
            }
          });
          _self.vars.tabFacetFieldName = res.tabFacet.fieldName;
        }
        totalResultsCount = totalResultsCount;

        if (res.searchFacets !== undefined) {
          _self.vars.searchFacetFilters = res.searchFacets;
          // searchFacets = res.searchFacets;
          searchFacets = _self.vars.searchFacetFilters;
        } else {
          res.searchFacets = [];
          _self.vars.searchByFacetFilters = res.searchFacets;
          searchFacets = _self.vars.searchFacetFilters;
        }
        $(".full-search-data-container").empty();
        if (res && res.results && res.resultType == "grouped") {
          var availableGroups = Object.keys(res.results);
          if (!(res.tabFacet || {}).buckets) {
            totalResultsCount = 0;
          }
          var reArrangeAvailableGroups = [];
          _self.vars.availableGroupsOrder.forEach((d) => {
            let group = availableGroups.find((g) => g === d);
            if (group) {
              reArrangeAvailableGroups.push(group);
            }
          });
          availableGroups = reArrangeAvailableGroups;
          if (availableGroups && availableGroups.length) {
            availableGroups.forEach((group) => {
              var results = res.results[group].data;
              if (!(res.tabFacet || {}).buckets) {
                totalResultsCount =
                  totalResultsCount + res.results[group].doc_count;
              }
              var groupName =
                group == "default_group" ? "defaultTemplate" : group;
              var dataObj = {
                facets: facets,
                searchFacets: searchFacets,
                originalQuery: res.originalQuery || "",
                customSearchResult: _self.customSearchResult,
                data: results,
                groupName: groupName,
                doc_count: res.results[group].doc_count,
              };
              var publishSearchData = "sa-" + groupName + "-search-data";
              if (!$(".empty-full-results-container").hasClass("hide")) {
                $(".empty-full-results-container").addClass("hide");
              }
              _self.pubSub.publish(publishSearchData, {
                container: ".full-search-data-container",
                isFullResults: true,
                selectedFacet: "all results",
                isLiveSearch: false,
                isSearch: false,
                dataObj,
              });
            });
            if (!$(".full-search-data-container").children().length) {
              if (_self.isDev) {
                $(".no-templates-defined-full-results-container").show();
              } else {
                $(".empty-full-results-container").removeClass("hide");
              }
            }
          } else {
            var dataObj = {
              facets: facets || [],
              searchFacets: searchFacets || [],
              originalQuery: res.originalQuery || "",
              customSearchResult: _self.customSearchResult,
              data: [],
              groupName: "defaultTemplate",
            };
            // _self.pubSub.publish('sa-defaultTemplate-search-data', { container: '.full-search-data-container', isFullResults: true, selectedFacet: 'all results', isLiveSearch: false, isSearch: false, dataObj });
            if (
              !(res.tasks || []).length &&
              selectedFacet == "all results" &&
              !((res.tasks || []).length && selectedFacet == "task")
            ) {
              $(".empty-full-results-container").removeClass("hide");
              $(".no-templates-defined-full-results-container").hide();
            }
          }

          // res.results = results;
        } else {
          var results = res.results.data;
          if (!(res.tabFacet || {}).buckets) {
            totalResultsCount = res.results.doc_count || 0;
          }
          // res.results = results;
          var dataObj = {
            facets: facets || [],
            searchFacets: searchFacets || [],
            originalQuery: res.originalQuery || "",
            customSearchResult: _self.customSearchResult,
            data: results,
            groupName: "defaultTemplate",
            doc_count: totalResultsCount,
          };
          if (totalResultsCount) {
            _self.pubSub.publish("sa-defaultTemplate-search-data", {
              container: ".full-search-data-container",
              isFullResults: true,
              selectedFacet: "all results",
              isLiveSearch: false,
              isSearch: false,
              dataObj,
            });
            if (!$(".empty-full-results-container").hasClass("hide")) {
              $(".empty-full-results-container").addClass("hide");
            }
          } else {
            $(".empty-full-results-container").removeClass("hide");
            $(".no-templates-defined-full-results-container").hide();
          }
        }
        if (Object.keys(res.results).length === 0 && res.tasks.length == 0) {
          $(".empty-full-results-container").removeClass("hide");
          $(".no-templates-defined-full-results-container").hide();
        }
        _self.vars.searchObject.liveData.facets = _self.vars.tabsList;
        setTimeout(function () {
          _self.bindStructuredDataTriggeringOptions();
        }, 500);
        _self.pubSub.publish("facet-selected", {
          selectedFacet: _self.vars.selectedFacetFromSearch || "all results",
        });
        // setTimeout( () => {
        //   _self.checkBoostAndLowerTimes();
        // }, 400);
      }
      _self.fullResultsScrollTop();
      $(".content-data-sec").scrollTop(0);
      $(".data-body-sec").scrollTop(0);
    });
};

FindlySDK.prototype.handlePaginationUI = function (selectedFacet, data) {
  var _self = this;
  if (
    selectedFacet == "web" &&
    _self.pageConfig.fullSearchInterface.type == "carousel"
  ) {
    $(".kore-sdk-pagination-div").hide();
  } else if (
    selectedFacet == "faq" &&
    _self.faqConfig.fullSearchInterface.type == "carousel"
  ) {
    $(".kore-sdk-pagination-div").hide();
  } else if (
    selectedFacet == "file" &&
    _self.documentConfig.fullSearchInterface.type == "carousel"
  ) {
    $(".kore-sdk-pagination-div").hide();
  } else if (
    selectedFacet == "data" &&
    (_self.structuredDataConfig.fullSearchInterface.type == "carousel" ||
      !_self.structuredDataConfig.fullSearchInterface.mapping)
  ) {
    $(".kore-sdk-pagination-div").hide();
  } else if (
    selectedFacet &&
    data.facets[selectedFacet] > 10 &&
    selectedFacet != "task"
  ) {
    // $('.kore-sdk-pagination-div').show();
  } else {
    $(".kore-sdk-pagination-div").hide();
  }
};

FindlySDK.prototype.facetReset = function (facetObj, facetData) {
  var _self = this;
  // _self.pubSub.subscribe('sa-search-facets', (msg, data) => {
  //   if(data){
  //     facetData = data;
  //   }
  // });
  //facetObj.position = "top";
  var facetTemplate = $(_self.facetFilter()).tmpl({
    position: facetObj.position,
    show: facetObj.show,
    searchFacets: facetData,
  });
  var facetTemplateTop = $(_self.facetFilterTop()).tmpl({
    position: facetObj.position,
    show: facetObj.show,
    searchFacets: facetData,
  });
  var facetTemplateTopIcon = $(_self.facetTemplateTopIcon()).tmpl({
    position: facetObj.position,
    show: facetObj.show,
    searchFacets: facetData,
  });
  if (facetObj.position == "right") {
    $("#rightFacetFilterId").empty().append(facetTemplate);
  } else if (facetObj.position == "left") {
    $("#leftFacetFilterId").empty().append(facetTemplate);
  } else {
    $("#topFacetFilterId").empty().append(facetTemplateTop);
    $("#topFacetIcon").empty().append(facetTemplateTopIcon);
    if (facetObj.show) {
      if (!$(".iffilteristop").hasClass("isTopAlignFilterAdded")) {
        $(".iffilteristop").addClass("isTopAlignFilterAdded");
      }
    } else {
      $(".iffilteristop").removeClass("isTopAlignFilterAdded");
    }
  }
  if (!$("body").hasClass("top-down")) {
    if (facetObj.show) {
      $(".fliter-right-btn").addClass("active-open");
    } else {
      $(".fliter-right-btn").removeClass("active-open");
    }
  }
  _self.markSelectedFilters();
};
FindlySDK.prototype.bindShowAllResultsTrigger = function (
  showAllHTML,
  facetData,
  data,
  restrictSelectFacet
) {
  var _self = this;
  //_self.pubSub.publish('sa-full-data-search')
  var selectedFacet = $(".active-tab").attr("classification")
    ? $(".active-tab").attr("classification")
    : "all results";
  if (_self.vars.selectedFacetFromSearch) {
    selectedFacet = _self.vars.selectedFacetFromSearch;
  }

  var slecetFacetFunc = function (selectedFacet) {
    var selectedFacet_temp = selectedFacet ? selectedFacet : "all results";
    var dataObj = data ? data.dataObj : _self.vars.searchObject.liveData;

    $("#loaderDIV").show();
    $(".kore-sdk-pagination-div").hide();
    _self.vars.isPriorfiltersApplied = false;
    _self.vars.searchCallWithSelectedFacet = true;
    _self.invokeSpecificSearch(selectedFacet);
    // $('.kore-sdk-pagination-div').show();
    // }
    // handle pagination UI from here
  };
  if (!restrictSelectFacet) {
    // slecetFacetFunc(selectedFacet);
  }
  setTimeout(() => {
    var arr = [];
    if (document.getElementsByClassName("filter-sec-tab").length) {
      for (
        var i = 0;
        i <
        document
          .getElementsByClassName("filter-sec-tab")[0]
          .getElementsByClassName("see-all-result-nav").length;
        i++
      ) {
        arr.push(
          document
            .getElementsByClassName("filter-sec-tab")[0]
            .getElementsByClassName("see-all-result-nav")
          [i].getAttribute("classification")
        );
      }
      arr.forEach((element, index) => {
        document
          .getElementsByClassName("filter-sec-tab")[0]
          .getElementsByClassName("see-all-result-nav")
        [index].classList.remove("active-tab");
      });
      arr.forEach((element, index) => {
        if (element == selectedFacet) {
          document
            .getElementsByClassName("filter-sec-tab")[0]
            .getElementsByClassName("see-all-result-nav")
          [index].classList.add("active-tab");
        }
      });
    }
  }, 100);
  

  $(".sdk-clear-all-facet")
    .off("click")
    .on("click", function (event) {
      event.stopPropagation();
      event.stopImmediatePropagation();

      if (_self.vars.selectedFiltersArr.length > 0) {
        _self.vars.selectedFiltersArr.forEach(function (filter) {
          $("#" + filter).prop("checked", false);
        });
        _self.vars.selectedFiltersArr = [];
        _self.vars.filterObject = [];
        _self.vars.countOfSelectedFilters = 0;
        _self.vars.isClearAllClickedInBottomUp = true;
        // _self.searchByFacetFilters(_self.vars.filterObject);
      }
      var hide = function () {
        $(".filter-data").hide();
        $(".filter-updated-count").hide();
      };
      // setTimeout(hide, 1000);
    });
 
  var removeActive = function (selectedFacet) {
    $(".see-all-result-nav").each(function (index, element) {
      if ($(element).attr("classification") == selectedFacet) {
        $(element).removeClass("active-tab");
      }
    });
  };

  //SDK Top Facet
  //Search facet
  $(".sdk-bottomup-search-facet")
    .off("change")
    .on("change", function (event) {
      var id = event.currentTarget.id;
      var bucket = [...facetData];
      var val = event.currentTarget.value;

      //var bucket = $(event.target).closest('.sdk-bottomup-search-face').attr('data-bucket');
    });

  $(showAllHTML)
    .off("click", ".kore-sdk-bottom-up-first")
    .on("click", ".kore-sdk-bottom-up-first", function () {
      // $('#loaderDIV').show();
      _self.vars.scrollPageNumber = 0;
      _self.invokeSpecificSearch(_self.vars.selectedFacetFromSearch);
    });
  $(showAllHTML)
    .off("click", ".kore-sdk-bottom-up-last")
    .on("click", ".kore-sdk-bottom-up-last", function () {
      // $('#loaderDIV').show();
      var totalPages;
      totalPages = $("#kore-total-page-number").text();
      if (totalPages) {
        _self.vars.scrollPageNumber = totalPages - 1;
      }
      _self.invokeSpecificSearch(_self.vars.selectedFacetFromSearch);
    });
  $(showAllHTML)
    .off("click", ".kore-sdk-bottom-up-next")
    .on("click", ".kore-sdk-bottom-up-next", function () {
      if (
        _self.vars.totalNumOfResults >
        (_self.vars.scrollPageNumber + 1) * 10
      ) {
        _self.vars.scrollPageNumber = _self.vars.scrollPageNumber + 1;
        $("#loaderDIV").show();
        _self.invokeSpecificSearch(_self.vars.selectedFacetFromSearch);
      }
    });
  $(showAllHTML)
    .off("click", ".kore-sdk-bottom-up-previous")
    .on("click", ".kore-sdk-bottom-up-previous", function () {
      if (_self.vars.scrollPageNumber > 0) {
        _self.vars.scrollPageNumber = _self.vars.scrollPageNumber - 1;
        $("#loaderDIV").show();
        _self.invokeSpecificSearch(_self.vars.selectedFacetFromSearch);
      }
    });
  $(showAllHTML)
    .off("keyup", ".kore-current-page-number")
    .on("keyup", ".kore-current-page-number", function (event) {
      var totalPages;
      totalPages = $("#kore-total-page-number").text();
      if (event.keyCode == 13 || event.which == 13) {
        if (parseInt(event.target.value) > 0) {
          if (parseInt(event.target.value) > parseInt(totalPages)) {
            _self.vars.scrollPageNumber = parseInt(totalPages) - 1;
          } else {
            _self.vars.scrollPageNumber = parseInt(event.target.value) - 1;
          }
        } else {
          $("#kore-current-page-number").val(_self.vars.scrollPageNumber + 1);
        }
        // $('#loaderDIV').show();
        _self.invokeSpecificSearch(_self.vars.selectedFacetFromSearch);
      }
    });
  // Search Facet

  $(showAllHTML)
    .off("click", ".search-task")
    .on("click", ".search-task", function (e) {
      e.stopPropagation();
      _self.hideBottomUpAllResults();
      var ele = $(e.target).closest(".search-task");
      var scrollBottom =
        $("#searchChatContainer").scrollTop() +
        $("#searchChatContainer").height();
      $("#searchChatContainer").animate({ scrollTop: scrollBottom });
      var taskName = e.currentTarget.title.toLowerCase();
      // var payload = $(e.target).attr('payload');
      var payload;
      if (
        _self.vars.searchObject &&
        _self.vars.searchObject.searchText &&
        _self.vars.searchObject.searchText.length
      ) {
        payload = b64EncodeUnicode(
          "Execute_" + _self.vars.searchObject.searchText
        );
        // payload = _self.vars.searchObject.searchText;
      } else {
        payload = $(e.currentTarget).attr("payload");
      }
      if (
        !_self.vars.searchObject.recentTasks.length ||
        (_self.vars.searchObject.recentTasks.length &&
          _self.vars.searchObject.recentTasks.indexOf(taskName.toLowerCase()) ==
          -1)
      ) {
        _self.vars.searchObject.recentTasks.unshift(taskName.toLowerCase());
      }
      var recentItem = [];
      recentItem.push(taskName);
      _self.vars.searchObject.recentTasks = _.uniq(
        recentItem.concat(_self.vars.searchObject.recentTasks)
      );
      if (
        _self.vars.searchObject.recentTasks &&
        _self.vars.searchObject.recentTasks.length
      ) {
        _self.vars.searchObject.recentTasks = _.filter(
          _self.vars.searchObject.recentTasks,
          function (task) {
            return task;
          }
        );
      }
      if (_self.vars.showingMatchedResults == true) {
        _self.captureClickAnalytics(
          e,
          $(e.currentTarget).attr("contentType"),
          "click",
          $(e.currentTarget).attr("contentId"),
          $(e.currentTarget).attr("id"),
          $(e.currentTarget).attr("title")
        );
      }
      window.localStorage.setItem(
        "recentTasks",
        JSON.stringify(_self.vars.searchObject.recentTasks)
      );
      if (!$("body").hasClass("top-down")) {
        _self.bindFrequentData();
      }
      if (_self.config.viaSocket) {
        // var childBotName = $(e.target).attr('childBotName')
        var nlMeta = {
          linkedBotNLMeta: {
            intent: $(e.currentTarget).attr("title"),
            seqLogId: $(e.currentTarget).attr("seqLogId") || null,
            childBotName: $(e.currentTarget).attr("childBotName") || null,
            isRefresh: true,
          },
        };
        _self.sendMessage(payload, null, nlMeta);
      }
      if (_self.isDev || _self.vars.loggedInUser) {
        _self.vars.searchObject.searchText =
          e.currentTarget.title.toLowerCase();
        _self.sendMessageToSearch("botAction");
      } else if (
        e.currentTarget.title.toLowerCase() === "pay bill" ||
        e.currentTarget.title.toLowerCase() === "pay credit card bill"
      ) {
        _self.userLogin(e.currentTarget.title.toLowerCase());
      } else {
        //_self.userLogin(e.target.title.toLowerCase());
      }
    });
};

FindlySDK.prototype.calculatePageNumber = function (selectedFacet, data) {
  var _self = this;
  var totalPages = 1;
  if (data && data.facets) {
    totalPages = data.facets[selectedFacet] / 10;
    if (totalPages) {
      var temp_totalPages;
      temp_totalPages = Math.round(totalPages);
      if (totalPages > temp_totalPages) {
        totalPages = temp_totalPages + 1;
      } else {
        totalPages = temp_totalPages;
      }
    } else {
      totalPages = 1;
    }
    $("#kore-current-page-number").val(_self.vars.scrollPageNumber + 1);
    $("#kore-total-page-number").text(totalPages);
  } else {
    $("#kore-current-page-number").val(1);
    $("#kore-total-page-number").text(1);
  }
  if (_self.vars.scrollPageNumber == 0) {
    $(".kore-sdk-bottom-up-previous").addClass("disabled");
    $(".kore-sdk-bottom-up-first").addClass("disabled");
  } else {
    $(".kore-sdk-bottom-up-previous").removeClass("disabled");
    $(".kore-sdk-bottom-up-first").removeClass("disabled");
  }
  if (data.facets[selectedFacet] < (_self.vars.scrollPageNumber + 1) * 10) {
    $(".kore-sdk-bottom-up-next").addClass("disabled");
    $(".kore-sdk-bottom-up-last").addClass("disabled");
  } else {
    $(".kore-sdk-bottom-up-next").removeClass("disabled");
    $(".kore-sdk-bottom-up-last").removeClass("disabled");
  }
};

