FindlySDK.prototype.appendSuggestions = function (autoComplete) {
  var _self = this;

  var url = this.API.autoSuggestionsURL;
  var type = "POST";
  var payload = {
    query: $("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val(),
    // "maxNumOfResults": 9,
    maxNumOfResults: searchConfigurationCopy?.querySuggestionsLimit||2,
    userId: _self.API.uuid,
    streamId: _self.API.streamId,
    lang: "en",
    isDev: _self.isDev,
  };
  payload.userId = this.bot.userInfo.userInfo.userId;
  payload.indexPipelineId = _self.API.indexpipelineId;
  var bearer =
    "bearer " + this.bot.options.accessToken ||
    this.API.jstBarrer ||
    "bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiIxMjM0NTY3ODkwIiwibmFtZSI6IkpvaG4gRG9lIiwiaWF0IjoxNTE2MjM5MDIyfQ.wrUCyDpNEwAaf4aU5Jf2-0ajbiwmTU3Yf7ST8yFJdqM";
  var headers = {};
  headers["Authorization"] = bearer;
  headers['Content-Type'] = "application/json";
  if (!_self.isDev) {
    if (_self.config.botOptions.assertion) {
      headers.auth = _self.config.botOptions.assertion;
    }
  }
  if (
    !($("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val()) ||
    (searchConfigurationCopy.querySuggestionsLimit == 0 &&
      !searchConfigurationCopy.autocompleteOpt)
  ) {
    _self.vars.previousAutosuggestionData = $("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val();
    return;
  }
  if (
    _self.vars.previousAutosuggestionData ==
    ($("body").hasClass("top-down")
      ? $(".search-top-down").val()
      : $(".bottom-up-search").val())
  ) {
    return;
  }
  _self.vars.previousAutosuggestionData = $("body").hasClass("top-down")
    ? $(".search-top-down").val()
    : $(".bottom-up-search").val();
  $.ajax({
    url: url,
    type: type,
    dataType: "json",
    headers: headers,
    data: JSON.stringify(payload),
    success: function (data) {
      if (!data.isBotLocked) {
        if (searchConfigurationCopy.querySuggestionsLimit) {
          var autoSuggestionHTML = $(
            _self.getAutoSuggestionTemplate()
          ).tmplProxy({
            suggestions: data.autoComplete.querySuggestions,
            querySuggestionsLimit:
              searchConfigurationCopy.querySuggestionsLimit ||
                searchConfigurationCopy.querySuggestionsLimit == 0
                ? searchConfigurationCopy.querySuggestionsLimit
                : 2,
          });
          // if ($('.search-body').find('.resultsOfSearch').length) {
          //   $('#autoSuggestionContainer').empty().append(autoSuggestionHTML);
          // }
          // else if (searchConfigurationCopy.liveSearchResultsLimit == 0) {
          //   $('#autoSuggestionContainer').empty().append(autoSuggestionHTML);
          // } else {
          //   $('#autoSuggestionContainer').empty().append(autoSuggestionHTML);
          // }
          if (!$("body").hasClass("top-down")) {
            if (data.autoComplete.querySuggestions.length) {
              $("#autoSuggestionContainer").empty().append(autoSuggestionHTML);
              _self.bindPerfectScroll(
                $("#autoSuggestionContainer"),
                ".bottom-to-top-suggestion",
                null,
                "bottom-up-suggestion"
              );
              $(".bottom-to-top-suggestion").animate(
                {
                  scrollTop: $(".bottom-to-top-suggestion").prop(
                    "scrollHeight"
                  ),
                },
                300
              );
            } else {
              $("#autoSuggestionContainer")
                .empty()
                .append(
                  '<div class="suggestion-search-data-parent"><div class="noquerySuggestionsFound">No suggestions found</div></div>'
                );
            }
            $(".parent-search-live-auto-suggesition").show();
            if ($(".search-body").hasClass("recent-search-show")) {
              $(".search-body").removeClass("recent-search-show");
            }
          }
        }
        if (!$("body").hasClass("top-down")) {
          _self.changeSearchContainerBackground();
        }
        if (searchConfigurationCopy.autocompleteOpt) {
          _self.pubSub.publish("sa-auto-suggest", data.autoComplete.typeAheads);
        }
        if (searchConfigurationCopy.querySuggestionsLimit) {
          _self.bindAutoSuggestionTriggerOptions(autoSuggestionHTML);
        }
        if ($("body").hasClass("top-down")) {
          if (searchConfigurationCopy.querySuggestionsLimit) {
            _self.showSuggestionbox(data.autoComplete.querySuggestions);
          }
          if (searchConfigurationCopy.autocompleteOpt) {
            $(".top-down-suggestion").show();
          } else {
            $(".top-down-suggestion").hide();
          }
        } else {
          if (searchConfigurationCopy.autocompleteOpt) {
            $(".bottom-up-suggestion").show();
          } else {
            $(".bottom-up-suggestion").hide();
          }
        }
      } else {
        if (!$("body").hasClass("top-down")) {
          $("#autoSuggestionContainer").empty();
          $(".parent-search-live-auto-suggesition").hide();
          if ($(".search-body").hasClass("recent-search-show")) {
            $(".search-body").removeClass("recent-search-show");
          }
        }
      }
    },
    error: function (err) { },
  });

  // _self.pubSub.publish('sa-auto-suggest', data.autoComplete.typeAheads);
};

