FindlySDK.prototype.initializeTopDown = function (
  findlyConfig,
  search_container,
  searchExperienceConfig
) {
  var _self = this;
  if (search_container && search_container.length) {
    _self.isDev = true;
  } else {
    _self.isDev = false;
  }
  var devMode = _self.isDev ? true : false;
  var searchConfiguration = _self.mapSearchConfiguration(
    searchExperienceConfig
  );
  var dataHTML = $(FindlySDK.prototype.getTopDownTemplate()).tmplProxy({
    devMode: devMode,
    searchConfig: searchConfiguration,
  });
  var container = search_container ? $("." + search_container) : $("body");
  // var container = $('body');
  $(container).append(dataHTML);
  var findlyConfig = findlyConfig
    ? findlyConfig
    : _self.config;
  findlyConfig.baseAPIServer = "http://localhost:4200";
  // var findlySdk = new FindlySDK(findlyConfig);
  $("#conversation-container").hide();
  $(".show-result").hide();
  findlyConfig.autoConnect = true;
  _self.initialize(findlyConfig, _self.isDev);
  _self.getRecentSearches(_self.API.recentSearchUrl, "GET");
  _self.initializeTopSearchTemplate();
  if (!$("body").hasClass("searchAssist-defaultTheme-kore")) {
    $("body").addClass("searchAssist-defaultTheme-kore")
  }
  if ($(".search-background-div").length) {
    $(".search-background-div").remove();
  }
  _self.addSearchText({
    container: "search-box-container",
    classes: (searchConfiguration.searchButtonEnabled && searchConfiguration.buttonPlacementPosition
      =="inside")?' search-btn-inside search-input':'search-input',
    placeholder: "Search here",
    showGreeting: false,
    microphone: true,
    defaultMicrophone: false,
    hideSearchIcon: false,
    autoSuggest: true,
    showButton: false,
    searchConfig: searchConfiguration,
    focusHandler: focusHandler,
  });

  _self.addSearchResult({
    pageContainer: "pages-container",
    pageTemplateId: "page-template",
    faqContainer: "mostly-asked-container",
    faqTemplateId: "faq-template",
    actionContainer: "actions-container",
    actionTemplateId: "actions-template",
    structuredDataContainer: ".structured-data-container",
    actionHandler: actionHandler,
    searchHandler: searchHandler,
  });

  _self.addSourceType({
    container: "top-down-tab-sec",
    templateId: "top-down-tabs-template",
    selectedClass: "active-tab",
    unSelectedClass: "un-selected-type",
  });
  _self.addConversationBox({
    container: "conversation-box-container",
    classes: "conv-input",
  });
  _self.addConversationContainer({
    container: "conversation-body",
  });
  _self.addFrequentlyUsedControl({
    container: "frequently-searched-box",
    templateId: "frequently-searched-template",
    searchConfig: searchConfiguration,
  });
  _self.addGreetingMsgControl({
    container: "greeting-msg-top-down",
    searchConfig: searchConfiguration,
  });
  _self.addCustomTemplateConfig();
  var searchText = "";
  function searchHandler(data) {
    var size =
      (data.files || []).length +
      (data.web || []).length +
      (data.faqs || []).length +
      (data.tasks || []).length;
    if (data.originalQuery) {
      searchText = data.originalQuery;
    }
    var text = "Showing " + size + ' results for "' + searchText + '"';
    $("#show-result").empty().append(text).show();
  }

  function actionHandler(e) {
    // _self.addConversationContainer({
    //     container: "conversation-body"
    // });
    $("#conversation-container").show();
    if ($(".search-container").hasClass("no-history")) {
      $(".search-container").removeClass("no-history");
    }
    $("#sa-conversation-box").prop("disabled", false);
    $("#sa-conversation-box").attr("placeholder", "Type message...");
    $("#action-title").empty();
    _self.setActionTitle(e.target.getAttribute("title"), "action-title");
  }

  $(".close-conv-icon")
    .off("click")
    .on("click", function (e) {
      closeConversation();
      _self.unlockBot();
    });
  function closeConversation() {
    $("#conversation-container").hide();
    $("#action-title").empty();
    $("#searchChatContainer").empty();
    if($("#conversation-container").hasClass("live-search-action-chat-container")){
      $('#search').focus();
      $("#conversation-container").removeClass("live-search-action-chat-container");

    }
  }
  function focusHandler() {
    if ($("body").hasClass("top-down")) {
      closeConversation();
    }
  }
  if (search_container && search_container.length) {
    _self.isDev = true;
  } else {
    _self.isDev = false;
  }
  _self.pubSub.subscribe("sa-show-live-search-suggestion", (msg, data) => {
    if (_self.vars.enterIsClicked) {
      $("#live-search-result-box").hide();
      return;
    }
    if ($("body").hasClass("top-down")) {
      if (
        $(".live-search-data-container").children().length ||
        $("#auto-query-box").children().length
      ) {
        $("#live-search-result-box").show();
      } else {
        $("#live-search-result-box").hide();
      }
    }
  });

  _self.pubSub.unsubscribe("sa-show-all-results-top-down");
  _self.pubSub.subscribe("sa-show-all-results-top-down", (msg, data) => {
    _self.vars.showingMatchedResults = true;
    _self.searchFacetsList([]);
    _self.invokeSearch();
    setTimeout(function () {
      $(".all-result-container").show();
      if (!$('body').hasClass('search-executed')) {
        $('body').addClass('search-executed');
      }
      //top-down-searc-facets active -start//
      _self.pubSub.publish("facet-selected", { selectedFacet: "all results" });
      //top-down-search-facets active -end//
      $("#live-search-result-box").hide();
      $("#frequently-searched-box").hide();
      $("#loaderDIV").hide();
      $('.back-search').show();
    }, 600);
  });
  $("#search-box-container")
    .off("focus", ".search-top-down")
    .on("focus", ".search-top-down", function (e) {
      if (!$(".search-top-down").val()) {
        _self.bindFrequentData();
      }
      if ($("#greeting-msg-top-down").length) {
        $("#greeting-msg-top-down").hide();
      }
    });
  if (_self.isDev) {
    $(".show_insights_top_down")
      .off("click", ".query_analytics_top_down")
      .on("click", ".query_analytics_top_down", function (event) {
        event.preventDefault();
        event.stopImmediatePropagation();
        if (_self.vars.searchObject && _self.vars.searchObject.searchText) {
          var responseObject = {
            type: "show",
            data: true,
            query: _self.vars.searchObject.searchText,
          };
          _self.parentEvent(responseObject);
          setTimeout(function () {
            $(".query_analytics_content").css("top", event.pageY - 50);
            $(".query_analytics_content").css(
              "left",
              event.pageX - 200 - event.offsetX
            );
            $(document).on("click", function (event) {
              if (!$(event.target).closest(".query_analytics_content").length) {
                $(".query_analytics_content").hide();
              }
            });
          }, 100);
        }
      });
  }
  var resultsContainerHtml = $(".all-product-details");
  _self.bindPerfectScroll(
    resultsContainerHtml,
    "#show-filters-added-data",
    null,
    "y",
    "facetsListContainer"
  );
  var conversationContainerHtml = $("#conversation-container");
  _self.bindPerfectScroll(
    conversationContainerHtml,
    "#conversations",
    null,
    "y",
    "conversationContainer"
  );
  if (!$("body").hasClass("searchAssist-defaultTheme-kore")) {
    $("body").addClass("searchAssist-defaultTheme-kore");
  }
  if (_self.isDev) {
    _self.initKorePicker(findlyConfig);
  }
};

