FindlySDK.prototype.suggestionSelectedByNavigationKeys = function (e) {
  var _self = this;
  if ($("body").hasClass("top-down")) {
    var $hlight = $(".suggestion-box.highlightSuggestion"),
      $div = $(".suggestion-box");
    if (e.keyCode == 40) {
      $hlight
        .removeClass("highlightSuggestion")
        .next()
        .addClass("highlightSuggestion");
      if ($hlight.next().length == 0) {
        $div.eq(0).addClass("highlightSuggestion");
      }
      if(!_self.vars.isQueryEntered){
        _self.vars.enteredQuery =  $('.search-top-down').val();
      }
      var querySuggestionId = $(
        ".suggestion-box.highlightSuggestion .sugg-query-box"
      ).attr("id");
      $(".search-top-down").val(querySuggestionId);
      $(".top-down-suggestion").val("");
      _self.vars.isQueryEntered = true;
    } else if (e.keyCode === 38) {
      $hlight
        .removeClass("highlightSuggestion")
        .prev()
        .addClass("highlightSuggestion");
      if ($hlight.prev().length == 0) {
        $div.eq(-1).addClass("highlightSuggestion");
      }
      if(!_self.vars.isQueryEntered){
        _self.vars.enteredQuery =  $('.search-top-down').val();
      }
      var querySuggestionId = $(
        ".suggestion-box.highlightSuggestion .sugg-query-box"
      ).attr("id");
      $(".search-top-down").val(querySuggestionId);
      $(".top-down-suggestion").val("");
      _self.vars.isQueryEntered = true;
    }
  } else {
    var $hlight = $(".search-suggested-title.highlightSuggestion"),
      $div = $(".search-suggested-title");
    if (e.keyCode == 40) {
      $hlight
        .removeClass("highlightSuggestion")
        .next()
        .addClass("highlightSuggestion");
      if ($hlight.next().length == 0 || $hlight.next().hasClass("ps__rail-x")) {
        $(".bottom-to-top-suggestion").animate({ scrollTop: 0 }, 300);
        $div.eq(0).addClass("highlightSuggestion");
      } else {
        $(".bottom-to-top-suggestion").animate(
          { scrollTop: $(".bottom-to-top-suggestion").scrollTop() + 34 },
          300
        );
      }
      if(!_self.vars.isQueryEntered){
        _self.vars.enteredQuery =  $('.bottom-up-search').val();
      }
      var querySuggestionId = $(
        ".search-suggested-title.highlightSuggestion"
      ).attr("suggestion");
      $(".bottom-up-search").val(querySuggestionId);
      $(".bottom-up-suggestion").val("");
      _self.vars.isQueryEntered = true;
    } else if (e.keyCode === 38) {
      $hlight
        .removeClass("highlightSuggestion")
        .prev()
        .addClass("highlightSuggestion");
      if ($hlight.prev().length == 0) {
        $div.eq(-1).addClass("highlightSuggestion");
        $(".bottom-to-top-suggestion").animate(
          { scrollTop: $(".bottom-to-top-suggestion").prop("scrollHeight") },
          300
        );
      } else {
        $(".bottom-to-top-suggestion").animate(
          { scrollTop: $(".bottom-to-top-suggestion").scrollTop() - 34 },
          300
        );
      }
      if(!_self.vars.isQueryEntered){
        _self.vars.enteredQuery =  $('.bottom-up-search').val();
      }
      var querySuggestionId = $(
        ".search-suggested-title.highlightSuggestion"
      ).attr("suggestion");
      $(".bottom-up-search").val(querySuggestionId);
      $(".bottom-up-suggestion").val("");
      _self.vars.isQueryEntered = true;
    }
  }
};
FindlySDK.prototype.clickNavigateToUrl = function (e) {
  var _self = this;
  if (e?.target) {
    // _self.captureClickAnalytics(
    //   e,
    //   $(e.currentTarget).closest(".faqs-shadow").attr("contentType"),
    //   "click",
    //   $(e.currentTarget).closest(".faqs-shadow").attr("contentId"),
    //   $(e.currentTarget).closest(".faqs-shadow").attr("id"),
    //   $(e.currentTarget).attr("title")
    // );
    if ($(e.target).is("a")) {
      if ($(e.target).attr("href")) {
        // window.open($(e.target).attr('href'), '_blank');
        var link = document.createElement("a");
        link.href = $(e.target).attr("href");
        (link.target = "_blank"), link.click();
        link.remove();
      }
    } else {
      if ($(e.target).closest(".click-to-navigate-url").attr("href")) {
        // window.open($(e.target).closest('.click-to-navigate-url').attr('href'), '_blank');
        var link = document.createElement("a");
        link.href = $(e.target)
          .closest(".click-to-navigate-url")
          .attr("href");
        (link.target = "_blank"), link.click();
        link.remove();
      }
    }
  }
};
