FindlySDK.prototype.trimSearchQuery = function (e) {
  var keyCode = e.keyCode || e.which;
          keyCode = Number(keyCode);
      if(e.target.selectionStart !== ($('body').hasClass('top-down') ? $('.search-top-down').val() : $('.bottom-up-search').val()).length && keyCode !== 13){
        return;
      }
  if (
    !$("body").hasClass("top-down") &&
    $(".bottom-up-search").val() &&
    $(".bottom-up-search").val().length == 1
  ) {
    $(".bottom-up-search").val($(".bottom-up-search").val().trim());
  } else if (
    !$("body").hasClass("top-down") &&
    $(".bottom-up-search").val() &&
    $(".bottom-up-search").val().length > 1
  ) {
    $(".bottom-up-search").val(
      $(".bottom-up-search").val().replace(/\s\s+/g, " ")
    );
    if ($(".bottom-up-search").val().charAt(0) == " ") {
      $(".bottom-up-search").val(
        $(".bottom-up-search")
          .val()
          .substring(1, $(".bottom-up-search").val().length)
      );
    }
  }
  if (
    $("body").hasClass("top-down") &&
    $(".search-top-down").val() &&
    $(".search-top-down").val().length == 1
  ) {
    $(".search-top-down").val($(".search-top-down").val().trim());
  } else if (
    $("body").hasClass("top-down") &&
    $(".search-top-down").val() &&
    $(".search-top-down").val().length > 1
  ) {
    $(".search-top-down").val(
      $(".search-top-down").val().replace(/\s\s+/g, " ")
    );
    if ($(".search-top-down").val().charAt(0) == " ") {
      $(".search-top-down").val(
        $(".search-top-down")
          .val()
          .substring(1, $(".search-top-down").val().length)
      );
    }
  }
};
