FindlySDK.prototype.botActionTrigger = function (e) {
  var _self = this;
  var scrollBottom =
    $("#searchChatContainer").scrollTop() +
    $("#searchChatContainer").height();
  $("#searchChatContainer").animate({ scrollTop: scrollBottom });
  if ($("body").hasClass("top-down")) {
    _self.actionHandler(e);
  }
  var taskName = e.currentTarget.title.toLowerCase();
  var payload;
  if (
    _self.vars.searchObject &&
    _self.vars.searchObject.searchText &&
    _self.vars.searchObject.searchText.length
  ) {
    payload = b64EncodeUnicode(
      "Execute_" + _self.vars.searchObject.searchText
    );
    // payload = _self.vars.searchObject.searchText;
  } else {
    payload = $(e.currentTarget).attr("payload");
  }
  if (
    !_self.vars.searchObject.recentTasks.length ||
    (_self.vars.searchObject.recentTasks.length &&
      _self.vars.searchObject.recentTasks.indexOf(taskName.toLowerCase()) ==
      -1)
  ) {
    _self.vars.searchObject.recentTasks.unshift(taskName.toLowerCase());
  }
  var recentItem = [];
  recentItem.push(taskName);
  _self.vars.searchObject.recentTasks = _.uniq(
    recentItem.concat(_self.vars.searchObject.recentTasks)
  );
  if (
    _self.vars.searchObject.recentTasks &&
    _self.vars.searchObject.recentTasks.length
  ) {
    _self.vars.searchObject.recentTasks = _.filter(
      _self.vars.searchObject.recentTasks,
      function (task) {
        return task;
      }
    );
  }
  if (_self.vars.showingMatchedResults == true) {
    _self.captureClickAnalytics(
      e,
      $(e.currentTarget).attr("contentType"),
      "click",
      $(e.currentTarget).attr("contentId"),
      $(e.currentTarget).attr("id"),
      $(e.currentTarget).attr("title")
    );
  }
  window.localStorage.setItem(
    "recentTasks",
    JSON.stringify(_self.vars.searchObject.recentTasks)
  );
  if (!$("body").hasClass("top-down")) {
    _self.bindFrequentData();
  }
  if (_self.config.viaSocket) {
    var nlMeta = {
      linkedBotNLMeta: {
        intent: $(e.currentTarget).attr("title"),
        seqLogId: $(e.currentTarget).attr("seqLogId") || null,
        childBotName: $(e.currentTarget).attr("childBotName") || null,
        isRefresh: true,
      },
    };
    _self.sendMessage(payload, null, nlMeta, true);
  }
  if ($("body").hasClass("top-down")) {
    _self.sendMessageToSearch(
      "user",
      null,
      null,
      _self.isDev == true ? true : false
    );
    _self.resetPingMessage();
    _self.resetSocketDisconnection();
  } else {
    _self.appendTextToSearchContainer('user', e.currentTarget.title);
  }
  if (_self.isDev || _self.vars.loggedInUser) {
    _self.vars.searchObject.searchText =
      e.currentTarget.title.toLowerCase();
    _self.sendMessageToSearch("botAction");
  } else if (
    e.currentTarget.title.toLowerCase() === "pay bill" ||
    e.currentTarget.title.toLowerCase() === "pay credit card bill"
  ) {
    _self.userLogin(e.currentTarget.title.toLowerCase());
  } else {
  }
}
FindlySDK.prototype.tabFacetTrigger = function (e, facetSelected) {
  if ($("body").hasClass("top-down")) {
    $(".content-data-sec").scrollTop(0);
  }else{
    $(".data-body-sec").scrollTop(0);
  }
  var _self = this;
  var selectedFacet = facetSelected;
  _self.vars.selectedFacetFromSearch = selectedFacet;
  var url = _self.API.searchUrl;
  var payload = {
    query: _self.vars.searchObject.searchText,
    // "maxNumOfResults": 9,
    maxNumOfResults: 5,
    userId: _self.API.uuid,
    streamId: _self.API.streamId,
    lang: "en",
    // "isDev": true,
    isDev: _self.isDev,
    searchRequestId: _self.vars.requestId,
  };
  if (_self.vars.isFilterModified) {
    payload['isFilterModified'] = true;
  }
  if (_self.isDev) {
    payload["customize"] = _self.vars.customizeView;
  }
  if (_self.vars.displaySortable) {
    payload['sortableFacets'] = [_self.vars.displaySortable];
  }
  if (_self.vars.filterObject.length > 0) {
    payload.filters = JSON.parse(JSON.stringify(_self.vars.filterObject));
  }
  if (
    _self.vars.selectedFacetFromSearch &&
    _self.vars.selectedFacetFromSearch !== "all results"
  ) {
    var tabConfig = {
      filter: {
        fieldName: _self.vars.tabFacetFieldName,
        facetValue: [_self.vars.selectedFacetFromSearch],
      },
    };
    payload.tabConfig = tabConfig;
  } else {
    let filters = payload.filters;
    for (let i = (filters || []).length - 1; i >= 0; i--) {
      if (
        filters[i].facetValue[0] == "faq" ||
        filters[i].facetValue[0] == "task" ||
        filters[i].facetValue[0] == "web" ||
        filters[i].facetValue[0] == "file" ||
        filters[i].facetValue[0] == "data"
      ) {
        payload.filters.splice(i, 1);
      }
    }
  }
  return new Promise((resolve, reject) => {
    _self
      .getFrequentlySearched(url, "POST", JSON.stringify(payload)).then(data => {
        _self.getMergedData(_self.vars.resultSettings, data, 'isFullResults').then((res) => {
          resolve(res);
        })
      })
      .catch((error) => {
        reject(error);
      })
  })
}
FindlySDK.prototype.facetCheckBoxClick = function(event){
  // SDK checkbox
  var  _self = this;
      if ($(event.target).is(":checked")) {
        _self.vars.selectedFiltersArr.push($(event.target).attr("id"));

        _self.vars.countOfSelectedFilters += 1;

        _self.filterResults(event, true);
      } else {
        _self.removeSelectFacetFilter(event, { id: $(event.target).attr("id"), name: $(event.target).attr("name"), facetName: $(event.target).attr("facetName"), fieldName: $(event.target).attr("fieldName"), fieldType: $(event.target).attr("fieldType") });
        var unselectedFilterID = $(event.target).attr("id");
        _self.vars.selectedFiltersArr.slice(0).forEach(function (filter) {
          if (filter == unselectedFilterID) {
            _self.vars.selectedFiltersArr.splice(
              _self.vars.selectedFiltersArr.indexOf(filter),
              1
            );
          }
        });

        _self.vars.countOfSelectedFilters -= 1;

        _self.filterResults(event, false);
      }
      return new Promise((resolve, reject) => {
        if (_self.vars.filterConfiguration.aligned == "top") {
          _self.countFunction().then((res) => {
            res.isFilterAlignedTop = true;
            resolve(res);
          })
          .catch((error) => {
            reject(error);
          })
        } else {
          resolve({isFilterAlignedTop:false});
        }
      });
}
FindlySDK.prototype.getSearchByFacetFilters = function() {
var  _self = this;
// if ($("body").hasClass("top-down")) {
  _self.vars.tempSelectedFiltersArr = _self.vars.selectedFiltersArr;
  _self.vars.tempSelectedFacetsList = _self.vars.selectedFacetsList;
  _self.vars.tempFilterObject = _self.vars.filterObject;
  _self.vars.scrollPageNumber = 0;
// }
_self.vars.isFilterModified = true;
var filterObject = _self.vars.filterObject;
var url = _self.API.searchUrl;
  var payload = {
    query: _self.vars.searchObject.searchText,
    // "maxNumOfResults": 9,
    maxNumOfResults: 5,
    lang: "en",
    // "isDev": true,
    isDev: _self.isDev,
    searchRequestId: _self.vars.requestId,
  };
  if (_self.vars.isFilterModified) {
    payload['isFilterModified'] = true;
  }
  if (_self.isDev) {
    payload["customize"] = _self.vars.customizeView;
  }
  if ((_self.vars.deselectedAutoFilters || []).length) {
    payload['deselectedAutoFilters'] = _self.vars.deselectedAutoFilters || [];
  }
  if (_self.vars.displaySortable) {
    payload['sortableFacets'] = [_self.vars.displaySortable];
  }
  if (filterObject.length > 0) {
    payload.filters = JSON.parse(JSON.stringify(filterObject));
  }
  var contentTypeFilter = {
    fieldName: "sys_content_type",
    name: "facetContentType",
    subtype: "value",
    multiselect: false,
    buckets: [],
  };
  if (
    _self.vars.selectedFacetFromSearch &&
    _self.vars.selectedFacetFromSearch !== "all results"
  ) {
    var tabConfig = {
      filter: {
        fieldName: _self.vars.tabFacetFieldName,
        facetValue: [_self.vars.selectedFacetFromSearch],
      },
    };
    payload.tabConfig = tabConfig;
  } else {
    let filters = payload.filters;
  }
return new Promise((resolve, reject) => {
  _self.getFrequentlySearched(url, "POST", JSON.stringify(payload)).then(data => {
    var totalResultsCount = 0;
    if (data.template.tabFacet && data.template.tabFacet.buckets && data.template.tabFacet.buckets.length) {
      data.template.tabFacet.buckets.forEach((tab) => {
        totalResultsCount = totalResultsCount + tab.doc_count;
        if (!tab.name) {
          tab.name = tab.key;
        }
      });
      _self.vars.tabFacetFieldName = data.template.tabFacet.fieldName;
    }
    totalResultsCount = totalResultsCount;
    _self.countTotalResults(data.template, 0);
    let facets = [];
    facets.push({
      key: "all results",
      doc_count: _self.vars.totalNumOfResults,
      name: "ALL",
    });
    facets = facets.concat((data.template.tabFacet || {}).buckets || []);
    facets = _self.rearrangeTabsList(facets);
    if ((data.template.tasks || []).length) {
      facets.push({
        key: "task",
        doc_count: (data.template.tasks || []).length,
        name: "Actions",
      });
    }
    facets.forEach((tab) => {
      if (tab && tab.key) {
        tab["className"] = tab.key.replaceAll(" ", "-");
      }
    });
    _self.vars.tabsList = facets; 
    let filterCount =0;
    if (
      _self.vars &&
      _self.vars.selectedFiltersArr &&
      _self.vars.selectedFiltersArr.length
    ) {
      filterCount = _self.vars.selectedFiltersArr.length;
    }
    _self.getMergedData(_self.vars.resultSettings, data, 'isFullResults').then((res) => {
        resolve({result:res,facets:_self.vars.tabsList,filterCount:filterCount,isFilterAlignedTop:_self.vars.filterConfiguration.aligned == "top"?true:false});
      })
    })
    .catch((error) => {
      reject(error);
    })
})
};
FindlySDK.prototype.clearAllFilters = function () {
  var _self = this;
  if (_self.vars.countOfSelectedFilters > 0) {
    $("#loaderDIV").show();
  }
  if (_self.vars.selectedFiltersArr.length > 0) {
    _self.vars.selectedFiltersArr.forEach(function (filter) {
      $("#" + filter).prop("checked", false);
    });
    _self.vars.selectedFiltersArr = [];
    _self.vars.filterObject = [];
    _self.vars.countOfSelectedFilters = 0;
    return new Promise((resolve, reject) => {
    _self.getSearchByFacetFilters().then((res) => {
      resolve(res);
    })
    .catch((error) => {
      reject(error);
    })
    })
  }
};
FindlySDK.prototype.facetRadioClick = function(event){
  var _self = this;
    if ($(event.target).is(":checked")) {
      var latest = $(event.target).attr("id");
      _self.vars["selectedFiltersRadio"].push($(event.target).attr("id")); //radio-non-multi-
      if (_self.vars.selectedFiltersRadio) {
        _self.vars.selectedFiltersRadio.forEach((element, index) => {
          if (index != _self.vars.selectedFiltersRadio.length - 1) {
            if (
              element.split("-")[1].split("")[0] ==
              latest.split("-")[1].split("")[0]
            ) {
              _self.vars.selectedFiltersRadio.splice(index, 1);
            }
          }
        });
      }
      _self.vars.selectedFiltersArr.forEach((element, index) => {
        if (
          $("#" + element).attr("name") &&
          $("#" + element)
            .attr("name")
            .split("-")[0] == "radio"
        ) {
          if (
            element.split("-")[1].split("")[0] ==
            latest.split("-")[1].split("")[0]
          ) {
            _self.vars.selectedFiltersArr.splice(index, 1);
          }
        }
      });
      if (_self.vars["selectedFiltersRadio"].length) {
        _self.vars["selectedFiltersRadio"].forEach((element) => {
          _self.vars.selectedFiltersArr.push(element);
        });
      }
      _self.vars.countOfSelectedFilters =
        _self.vars.selectedFiltersArr.length;

      _self.filterResults(event, false);
      return new Promise((resolve, reject) => {
      if (_self.vars.filterConfiguration.aligned == "top") {
        _self.countFunction().then((res) => {
          res.isFilterAlignedTop = true;
          resolve(res);
        })
        .catch((error) => {
          reject(error);
        })
      } else {
        resolve({isFilterAlignedTop:false});
      }
    });
    }
}
FindlySDK.prototype.countFunction = function(){
  var _self = this;
  var facetData = _self.vars.searchFacetFilters;
    if (facetData.length) {
      var arr = [];
      var mainArr = [];
      if (_self.vars.selectedFiltersArr) {
        _self.vars.selectedFiltersArr.forEach((element, index) => {
          arr.push(element.split("-")[1].split("")[0]);
        });
        arr = new Set(arr);
        arr = [...arr];
      }
      if (_self.vars.selectedFiltersArr.length) {
        for (let i = 0; i <= arr.length; i++) {
          var mulArr = [];
          var obj = {};
          _self.vars.selectedFiltersArr.forEach((element, index) => {
            if (i == element.split("-")[1].split("")[0]) {
              mulArr.push(element.split("-")[1].split("")[0]);
            }
          });
          mainArr.push(mulArr);
        }
      }
    }
    for (let i = 0; i < facetData.length; i++) {
      facetData[i]["countPerGroup"] = mainArr;
    }
     _self.vars.searchFacetFilters = facetData; 
    return new Promise((resolve, reject) => {
        resolve({facetData:facetData});
      })
}

