FindlySDK.prototype.sortableFacetClick = function (event,displaySortable){
  var _self = this;
  _self.vars.displaySortable = displaySortable;
  return new Promise((resolve, reject) => {
  var url = _self.API.searchUrl;
  var currentDate = new Date();
  var dateTime = currentDate.getDate() + "/"
    + (currentDate.getMonth() + 1) + "/"
    + currentDate.getFullYear() + ", "
    + currentDate.getHours() + ":"
    + currentDate.getMinutes() + ":"
    + currentDate.getSeconds();

  var payload = {
    "query": _self.vars.searchObject.searchText,
    // "maxNumOfResults": 9,
    "maxNumOfResults": 5,
    "userId": _self.API.uuid,
    "streamId": _self.API.streamId,
    "lang": "en",
    // "isDev": true,
    "isDev": _self.isDev,
    "searchRequestId": _self.vars.requestId
  }
  if (_self.vars.isFilterModified) {
    payload['isFilterModified'] = true;
  }
  if (_self.isDev) {
    payload['customize'] = _self.vars.customizeView;
  }
  if (_self.vars.displaySortable) {
    payload['sortableFacets'] = [_self.vars.displaySortable];
  }


  if (_self.vars.filterObject.length > 0) {
    payload.filters = JSON.parse(JSON.stringify(_self.vars.filterObject));
  }

  if (_self.vars.selectedFacetFromSearch && _self.vars.selectedFacetFromSearch !== 'all results') {
    var tabConfig = {
      "filter": {
        "fieldName": _self.vars.tabFacetFieldName,
        "facetValue": [_self.vars.selectedFacetFromSearch]
      }
    }
    payload.tabConfig = tabConfig;
  } else {
    let filters = payload.filters;
    for (let i = (filters || []).length - 1; i >= 0; i--) {
      if (filters[i].facetValue[0] == 'faq' || filters[i].facetValue[0] == 'task' || filters[i].facetValue[0] == 'web' || filters[i].facetValue[0] == 'file' || filters[i].facetValue[0] == 'data') {
        payload.filters.splice(i, 1);
      }
    }
  }

  if (_self.vars.resultRankingActionPerformed == true) {
    _self.vars.resultRankingActionPerformed = false;
  }

  if (_self.vars.showingMatchedResults == true) {
      _self.getFrequentlySearched(url, "POST", JSON.stringify(payload)).then(data => {
        var totalResultsCount = 0;
        if (data.template.tabFacet && data.template.tabFacet.buckets && data.template.tabFacet.buckets.length) {
          data.template.tabFacet.buckets.forEach((tab) => {
            totalResultsCount = totalResultsCount + tab.doc_count;
            if (!tab.name) {
              tab.name = tab.key;
            }
          });
          _self.vars.tabFacetFieldName = data.template.tabFacet.fieldName;
        }
        totalResultsCount = totalResultsCount;
        _self.countTotalResults(data.template, 0);
        let facets = [];
        facets.push({
          key: "all results",
          doc_count: _self.vars.totalNumOfResults,
          name: "ALL",
        });
        facets = facets.concat((data.template.tabFacet || {}).buckets || []);
        facets = _self.rearrangeTabsList(facets);
        if ((data.template.tasks || []).length) {
          facets.push({
            key: "task",
            doc_count: (data.template.tasks || []).length,
            name: "Actions",
          });
        }
        facets.forEach((tab) => {
          if (tab && tab.key) {
            tab["className"] = tab.key.replaceAll(" ", "-");
          }
        });
        _self.vars.tabsList = facets; 
        let filterCount =0;
        if (
          _self.vars &&
          _self.vars.selectedFiltersArr &&
          _self.vars.selectedFiltersArr.length
        ) {
          filterCount = _self.vars.selectedFiltersArr.length;
        }
        _self.getMergedData(_self.vars.resultSettings, data, 'isFullResults').then((res) => {
            resolve({result:res,facets:_self.vars.tabsList,filterCount:filterCount,isFilterAlignedTop:_self.vars.filterConfiguration.aligned == "top"?true:false, displaySortable:_self.vars.displaySortable,sortableFacetList:_self.vars.sortableFacetList || [] });
          })
        })
        .catch((error) => {
          reject(error);
        })
  }
  else {
    if (_self.vars.searchObject && _self.vars.searchObject.searchText) {
      var responseObject = { 'type': 'fullResult', data: true, query: _self.vars.searchObject.searchText }
      _self.parentEvent(responseObject);
    }

    $(".custom-insights-control-container").hide();
    // _self.showAllResults();
    // setTimeout(() => {
    //   _self.bindCustomizeAction();
    // }, 300);
  }
 })
}
FindlySDK.prototype.getJWTWithoutAPIKey = function (options, callback) {
  var jsonData = {
    "clientId": options.clientId,
    "clientSecret": options.clientSecret,
    "identity": options.userIdentity,
    "aud": "",
    "isAnonymous": false
  };
  return $.ajax({
    url: options.JWTUrl,
    type: 'post',
    data: jsonData,
    dataType: 'json',
    success: function (data) {
      options.assertion = data.jwt;
      if (callback) {
        callback(null, options);
      }
    },
    error: function (err) {
    }
  });
}
FindlySDK.prototype.getJWTByAPIKey = function (options, callback) {
  var _self = this;
  var apiKey = '';
  if (options.apiKey) {
    apiKey = options.apiKey;
  }
  var jsonData = {};
  return $.ajax({
    url: options.koreAPIUrl + 'websdk/' + apiKey,
    type: 'post',
    data: jsonData,
    dataType: 'json',
    success: function (data) {

      options.assertion = data.jwt;

      if (callback) {
        callback(null, options);
      }
    },
    error: function (err) {
    }
  });
}
FindlySDK.prototype.setupInternalAssertionFunction = function () {
  var _self = this;
  _self.getJWTWithoutAPIKey(_self.config.botOptions).then(function (res) {
    _self.config.botOptions.callback(null, _self.config.botOptions);
    // _self.emit(_self.EVENTS.JWT_SUCCESS, res);
    _self.setJWT(res.jwt);
    if (_self.vars.isHostedSdk) {
      _self.initSearchAssistSDK(_self.config);
      _self.vars.isHostedSdk = false;
    }
  }, function (errRes) {
  });
}
FindlySDK.prototype.setJWT = function (jwtToken) {
  var _self = this;
  const options = _self.config.botOptions;
  options.assertion = jwtToken;
};
FindlySDK.prototype.setupInternalAssertionFunctionWithAPIKey = function () {
  var _self = this;
  _self.getJWTByAPIKey(_self.config.botOptions).then(function (res) {
    _self.config.botOptions.callback(null, _self.config.botOptions);
    // _self.emit(_self.EVENTS.JWT_SUCCESS, res);
    _self.setJWT(res.jwt);
    _self.config.botOptions.searchIndexID = res.botInfo.searchIndexId;
    if (res.botInfo) {
      _self.config.botOptions.botInfo.chatBot = res.botInfo.name;
      _self.config.botOptions.botInfo.taskBotId = res.botInfo._id;
      _self.config.botOptions.clientId = res.botInfo.clientId;
      _self.config.botOptions.clientSecretId = res.botInfo.clientSecretId;
    }
    if (res.identity) {
      _self.config.botOptions.userIdentity = res.identity;
    }
    if (_self.vars.isHostedSdk) {
      _self.initSearchAssistSDK(_self.config);
      _self.vars.isHostedSdk = false;
    }
  }, function (errRes) {
  });
}
FindlySDK.prototype.show = function (config) {
  var _self = this;
  _self.vars.isHostedSdk = true;
  if (config && config.API_KEY_CONFIG && config.API_KEY_CONFIG.KEY != 'YOUR_API_KEY') {
    _self.config.botOptions["apiKey"] = config["API_KEY_CONFIG"].KEY;
  }
  _self.config.botOptions.assertionFn = _self.getAssertionToken.bind(this);
  _self.initKoreSDK();
};
FindlySDK.prototype.getAssertionToken = function (options, callback) {
  var _self = this;
  options.callback = callback;
  if (_self.config && _self.config.API_KEY_CONFIG && _self.config.API_KEY_CONFIG.KEY !== 'YOUR_API_KEY') {
    _self.setupInternalAssertionFunctionWithAPIKey();
  } else {
    _self.setupInternalAssertionFunction();
  }
}

FindlySDK.prototype.autoSelectFacetFilter = function () {
  var _self = this;
  //_self.vars.selectedFiltersArr = [];
  _self.vars.selectedFacetsList = [];
  if (_self.vars.searchFacetFilters) {
    for (var i = 0; i < _self.vars.searchFacetFilters.length; i++) {
      if (_self.vars.searchFacetFilters[i].buckets) {
        for (var j = 0; j < _self.vars.searchFacetFilters[i].buckets.length; j++) {
          if (_self.vars.searchFacetFilters[i].buckets[j].auto_select) {
            if (_self.vars.selectedFiltersArr.indexOf("checkbox-" + i + j) !== -1) {// Id of the facet filter
            } else {
              _self.vars.selectedFiltersArr.push("checkbox-" + i + j)
            }
            var obj = {
              fieldName: _self.vars.searchFacetFilters[i].facetName,
              fieldType: _self.vars.searchFacetFilters[i].facetType,
              id: "checkbox-" + i + j,
              name: _self.vars.searchFacetFilters[i].buckets[j].key
            };
            _self.vars.selectedFacetsList.push(obj);

            //filter object pushing autoseletFacets starts//
            var responsePayload = {};
            var fieldMatches = false;
            responsePayload.fieldName = _self.vars.searchFacetFilters[i].fieldName;
            responsePayload.subtype = _self.vars.searchFacetFilters[i].subtype;
            responsePayload.name = _self.vars.searchFacetFilters[i].name;
            if (_self.vars.searchFacetFilters[i].subtype == 'value') {
              responsePayload.facetValue = [];
              responsePayload.facetValue[0] = _self.vars.searchFacetFilters[i].buckets[j].key;
            }
            else {
              // var from = $(event.target).attr('data-from');
              // var to = $(event.target).attr('data-to');
              responsePayload.facetRange = [];
              // key = { 'from': 0, 'to': 100 };
              // key = { 'from': parseInt(from), 'to': parseInt(to) };
              responsePayload.facetRange[0] = _self.vars.searchFacetFilters[i].buckets[j].key;
            }
            if (_self.vars.filterObject.length == 0) {
              _self.vars.filterObject.push(responsePayload);
              fieldMatches = true;
            }
            if (_self.vars.filterObject.length > 0 && fieldMatches == false) {
              _self.vars.filterObject.forEach(function (element) {
                if (responsePayload.fieldName == element.fieldName && responsePayload.subtype == element.subtype) {
                  if (element.subtype == 'value') {
                    var index = element.facetValue.findIndex((d) => d == _self.vars.searchFacetFilters[i].buckets[j].key)
                    if (index == -1) {
                      element.facetValue.push(_self.vars.searchFacetFilters[i].buckets[j].key);
                    }
                  }
                  else {
                    var index = element.facetRange.findIndex((d) => d == _self.vars.searchFacetFilters[i].buckets[j].key)
                    if (index == -1) {
                      element.facetRange.push(_self.vars.searchFacetFilters[i].buckets[j].key);
                    }
                  }
                  fieldMatches = true;
                }
              })

            }
            if (fieldMatches == false) {
              _self.vars.filterObject.push(responsePayload);
            }
            //filter object pushing autoseletFacets ends//
          }
        }
      }
    }
  }
  

}
FindlySDK.prototype.removeAutoselectedFilterFacets = function (event) {
  var _self = this;
  _self.vars.searchFacetFilters.forEach((facet) => {
    facet['buckets'].forEach((d) => {
      if (d.auto_select) {
        var index = _self.vars.deselectedAutoFilters.findIndex((d) => d.fieldName == facet.fieldName);
        if (index == -1) {
          _self.vars.deselectedAutoFilters.push(
            {
              "fieldName": facet.fieldName,
              "subtype": facet.subtype,
              "name": facet.name,
              "facetValue": [d.key]
            }

          );
        } else {
          _self.vars.deselectedAutoFilters[index].facetValue.push(d.key)
        }
        d.auto_select = false;
      }
    })
  })
}
FindlySDK.prototype.removeSelectFacetFilter = function (event, selectedFacet) {
  var _self = this;
  var key = $(event.target).attr('name');
  if ($('.topdown-search-main-container').length) {
    if ($('.sdk-filter-radio-top-down').length && $(event.target).hasClass('sdk-filter-radio-top-down')) {
      key = $(event.target).attr('value');
    }
  }
  var facetMatched = false;
  if (_self.vars.deselectedAutoFilters.length) {
    for (var i = 0; i < _self.vars.deselectedAutoFilters.length; i++) {
      if (_self.vars.deselectedAutoFilters[i].fieldName == selectedFacet.fieldName && _self.vars.deselectedAutoFilters[i].subtype == selectedFacet.fieldType && _self.vars.deselectedAutoFilters[i].name == selectedFacet.facetName) {
        facetMatched = true;
        var keyMatched = false;
        for (var j = 0; j < _self.vars.deselectedAutoFilters[i].facetValue.length; j++) {
          if (key == _self.vars.deselectedAutoFilters[i].facetValue[j]) {
            keyMatched = true;
          }
        }
        if (!keyMatched) {
          _self.vars.deselectedAutoFilters[i].facetValue.push(key);
        }
      }
    }

  }
  if (!facetMatched) {
    _self.vars.deselectedAutoFilters.push(
      {
        "fieldName": selectedFacet.fieldName,
        "subtype": selectedFacet.fieldType,
        "name": selectedFacet.facetName,
        "facetValue": [key]
      }

    );
  }

  if (_self.vars.searchFacetFilters) {
    for (var i = 0; i < _self.vars.searchFacetFilters.length; i++) {
      if (_self.vars.searchFacetFilters[i].buckets) {
        for (var j = 0; j < _self.vars.searchFacetFilters[i].buckets.length; j++) {
          if (selectedFacet.fieldName = _self.vars.searchFacetFilters[i].fieldName && _self.vars.searchFacetFilters[i].buckets[j].auto_select && key == _self.vars.searchFacetFilters[i].buckets[j].key) {
            //filter object removing autoseletFacets starts//
            delete _self.vars.searchFacetFilters[i].buckets[j].auto_select
            //filter object pushing autoseletFacets ends//
          }
        }
      }
    }
  }

}
FindlySDK.prototype.backToSearchClickEvent = function (event) {
  var _self = this;
  $(".all-result-container").hide();
  if ($('body').hasClass('search-executed')) {
    $('body').removeClass('search-executed');
  }
      $("body").removeClass("showFullResults");
      $("#search").val("");
      $("#suggestion").val("");
      $(".top-down-suggestion").val("");
      $(".search-top-down").val("");
      $(".full-search-data-container").empty();
      $(".skelton-load-img").hide();
      $('.cancel-search').hide();
      if ($('body').hasClass('search-executed')) {
        $('.search-icon-btn').show();
      }
      _self.destroy();
}
FindlySDK.prototype.getFeedBackResult = function () {
  var _self = this;
  var url = _self.API.feedbackGetUrl;
  var query_text = $('.search-top-down').val() ? $('.search-top-down').val() : _self.vars.searchObject.searchText;
  var payload =
  {
  "query": query_text,
  "userId": this.bot.userInfo.userInfo.userId
  }
  var headers = {};
  var bearer = "bearer " + this.bot.options.accessToken;
  headers["Authorization"] = bearer;
  headers["Content-Type"] = "application/json";
  headers.auth = _self.config.botOptions.assertion;
  return $.ajax({
  url: url,
  type: 'post',
  dataType: 'json',
  headers: headers,
  data: JSON.stringify(payload),
  success: function (data) {
  if (data) {
  if ($('body').hasClass('top-down')) {
  $('.feedback-top-down-full').css('display', 'block');
  _self.vars.feedBackType = data.event;
  }
  else {
  $('.bottom-up-show-all').css('visibility', 'visible');
  _self.vars.feedBackType = data.event;
  }
  }
  if (data.event === null) {
  $('.thumbs-up-top-down-blue, .thumbs-up-top-down-red').hide();
  $('.thumbs-up-top-down-black,.thumbs-down-top-down-black').show();
  }
  else {
  if (data.event === 'thumbsUp') {
  $('.thumbs-up-top-down-black, .thumbs-up-top-down-red').hide();
  $('.thumbs-up-top-down-blue,.thumbs-down-top-down-black').show();
  }
  else {
  $('.thumbs-down-top-down-black, .thumbs-up-top-down-blue').hide();
  $('.thumbs-up-top-down-black,.thumbs-up-top-down-red').show();
  }
  }
  _self.feedBackResultEvents();
  },
  error: function (err) {
  }
  })
  }
  FindlySDK.prototype.updateFeedBackResult = function (type, text) {
  var _self = this;
  var url = _self.API.feedbackPostUrl;
  var payload = {
  "feedbackLevel": "query",
  "event": type,
  "feedbackLevel": "query",
  "indexPipelineId": _self.vars.experimentsObject.indexPipelineId,
  "searchIndexId":_self.config.botOptions ? _self.config.botOptions.searchIndexID : '',
  "isDev": _self.isDev,
  "query": text ? text : _self.vars.searchObject.searchText,
  "queryPipelineId": _self.vars.experimentsObject.queryPipelineId,//_self.API.pipelineId,
  "relay": "default",
  "searchRequestId": _self.vars.previousSearchObj.requestId,
  "streamId": _self.API.streamId
  }
  var headers = {};
  var bearer = "bearer " + this.bot.options.accessToken;
  headers["Authorization"] = bearer;
  headers["Content-Type"] = "application/json";
  headers.auth = _self.config.botOptions.assertion;
  return $.ajax({
  url: url,
  type: 'post',
  dataType: 'json',
  headers: headers,
  data: JSON.stringify(payload),
  success: function (data) {
  if (data) {
  _self.vars.feedBackType = type;
  }
  },
  error: function (err) {
  }
  })
  }
  FindlySDK.prototype.feedBackResultEvents = function () {
  var _self = this;
  $('.bottom-up-show-all').off('click').on('click', function (event) {
  if($(event.target).hasClass('helpfull-title') || $(event.target).hasClass('bottom-up-show-all')){
  return;
  }
  const type = event.target.alt === 'thumb-up' ? 'thumbsUp' : 'thumbsDown';
  const text = $('.search-top-down').val();
  if (type === 'thumbsUp') {
  $('.thumbs-up-top-down-black').hide();
  $('.thumbs-up-top-down-blue').show();
  $('.thumbs-down-top-down-black').show();
  $('.thumbs-up-top-down-red').hide();
  }
  else if (type === 'thumbsDown') {
  $('.thumbs-down-top-down-black').hide();
  $('.thumbs-up-top-down-red').show();
  $('.thumbs-up-top-down-black').show();
  $('.thumbs-up-top-down-blue').hide();
  }
  _self.updateFeedBackResult(type, text);
  });
  }
