        /* best match response check start*/
        FindlySDK.prototype.isBestMatchResponse = function (res) {
          var _self = this;
          var textMsge = '';
          if (res && res.results && res.resultType == "grouped") {
            var availableGroups = Object.keys(res.results);
            _self.vars.availableGroupsOrder = availableGroups;
            if (availableGroups && availableGroups.length) {
              availableGroups.forEach((group) => {
                if (res.results[group].data[0].sys_content_type == 'faq' && res.results[group].data[0].bestMatch) {
                  textMsge = res.results[group].data[0].faq_answer;
                }
              })
            }
          } else {
            if (res.results.data[0].sys_content_type == 'faq' && res.results.data[0].bestMatch) {
              textMsge = res.results.data[0].faq_answer;
            }
          }
          return textMsge;
        }
